import { useAPI } from '../../context/apiContext';
const AboutSourceLib = ({ lib }) => {
    const { sourceLibraries } = useAPI();

    return (
        <>
            <div className="px-4 mx-auto my-12 max-w-7xl">
                <p className="uppercase md:text-xl mb-7">
                    ABOUT THE {lib} SOURCE LIBRARY{' '}
                </p>
                <div className="grid gap-3 md:grid-cols-12 md:gap-7 ">
                    <div className="md:col-span-4">
                        <img
                            src={`${process.env.REACT_APP_DOMAIN}wp-content/plugins/asian-legacy-library-plugin/library-plugin-images/about.png`}
                            className="w-full"
                            alt=""
                        />
                    </div>
                    <div className="md:col-span-8">
                        <p className="text-base leading-7 text-gray-800">
                            {sourceLibraries.map(
                                (item, index) =>
                                    item?._source?.[
                                        'bibframe:identifier@source'
                                    ] === lib && (
                                        <>
                                            {item?._source?.[
                                                'bibframe:note@description'
                                            ] && (
                                                <div
                                                    className="mb-3"
                                                    dangerouslySetInnerHTML={{
                                                        __html: item?._source?.[
                                                            'bibframe:note@description'
                                                        ]
                                                    }}
                                                />
                                            )}
                                            {item?._source?.[
                                                'bibframe:note@notes'
                                            ] && (
                                                <div
                                                    dangerouslySetInnerHTML={{
                                                        __html: item?._source?.[
                                                            'bibframe:note@notes'
                                                        ]
                                                    }}
                                                />
                                            )}
                                        </>
                                    )
                            )}
                        </p>
                    </div>
                </div>
            </div>
        </>
    );
};
export default AboutSourceLib;
