import React, { useContext, useState, useEffect, createContext } from 'react';

const APIContext = createContext();

export const useDebounce = function (value, delay) {
    const [debouncedValue, setDebouncedValue] = useState(value);

    useEffect(() => {
        const handler = setTimeout(() => {
            setDebouncedValue(value);
        }, delay);

        return () => {
            clearTimeout(handler);
        };
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [value]);

    return debouncedValue;
};

function APIContextProvider({ children }) {
    // eslint-disable-next-line no-unused-vars
    //Entry Search states
    const [init, setInit] = useState(false);
    const [data, setData] = useState([]);
    const [aggregations, setAggregations] = useState([]);
    const [isLoading, setIsLoading] = useState(false);
    const [searchQuery, setSearchQuery] = useState('');
    const [recentSearch, setRecentSearch] = useState([]);
    const [searchField, setSearchField] = useState('');
    const [showAdvanceSearch, setShowAdvanceSearch] = useState(-1);
    const [fetchURL, setFetchURL] = useState('');

    //Filter states
    const [languageFilter, setLanguageFilter] = useState([]);
    const [assetsFilter, setAssetsFilter] = useState([]);
    const [subjectTree, setSubjectTree] = useState([]);
    const [sourceFilter, setSourceFilter] = useState('');
    const [collectionFilter, setCollectionFilter] = useState('');
    const [collectionRamachandraFilter, setCollectionRamachandraFilter] =
        useState('');
    const [authorFilter, setAuthorFilter] = useState([]);

    // Advance filter states
    const [treeLanguage, setTreeLanguage] = useState('');
    const [filterTree, setFilterTree] = useState([]);
    const [activeOperators, setActiveOperators] = useState({ val: [] });
    const [values, setValues] = useState({ val: [] });
    const [hasQuery, setHasQuery] = useState(false);
    // const [nearThis, setNearThis] = useState('');
    const [nearThat, setNearThat] = useState('');
    const [nearBy, setNearBy] = useState(4);

    // Pagination states
    const [totalRecord, setTotalRecord] = useState(0);
    const [activePage, setActivePage] = useState(1);
    const pageSize = 10;

    //Single page
    const [single, setSingle] = useState();
    const [relatedResources, setRelatedResources] = useState([]);
    const [globalSearch, setGlobalSearch] = useState('');
    const [allResources, setAllResources] = useState([]);
    const [singleData, setSingleData] = useState([]);

    //Source Libraries
    const [sourceLibraries, setSourceLibraries] = useState([]);

    //Collections
    const [collections, setCollections] = useState([]);

    /**
     * Build subject tree on advance filter
     **/
    useEffect(() => {
        async function getSubjectTree() {
            setIsLoading(true);
            let url = `https://api.asianlegacylibrary.org/resources/subjecttree?language=${treeLanguage}`;

            const resp = await fetch(url, {
                method: 'GET',
                headers: {
                    'Content-Type': 'application/json'
                }
            });
            const json = await resp.json();
            setSubjectTree(json?.SubjectTree || []);

            setIsLoading(false);
        }
        if (showAdvanceSearch === 1 && treeLanguage !== '') {
            getSubjectTree();
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [showAdvanceSearch, treeLanguage]);

    /**
     *   Set blank cookie for recent searches
     **/
    useEffect(() => {
        let item1 = localStorage.getItem('recentSearch1');
        let item2 = localStorage.getItem('recentSearch2');
        let item3 = localStorage.getItem('recentSearch3');

        /** 
         //set initial cookie
        **/
        if (item1 === null || item1 === '') {
            localStorage.setItem('recentSearch1', '');
        }
        if (item2 === null || item2 === '') {
            localStorage.setItem('recentSearch2', '');
        }
        if (item3 === null || item3 === '') {
            localStorage.setItem('recentSearch3', '');
        }
        setRecentSearch([item1, item2, item3]);

        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [init]);

    /**
     * Fetch data
     **/
    async function fetchData() {
        setInit(true);
        if (isLoading) {
            return;
        }

        setIsLoading(true);
        setAllResources([]);
        setShowAdvanceSearch(-1);
        let url;
        if (activeOperators.val.indexOf('near') === -1) {
            url = `https://api.asianlegacylibrary.org/resources?q=${searchQuery}`;
        } else {
            url = `https://api.asianlegacylibrary.org/resources?q=`;
        }

        //Build near query string
        setHasQuery(false);
        if (values.val.length > 0 && activeOperators.val.length > 0) {
            let notStr;
            let orStr;
            let andStr;
            if (activeOperators.val.indexOf('near') !== -1) {
                activeOperators.val.forEach((item, index) => {
                    if (item === 'near') {
                        notStr = `,near(${searchQuery}:${nearThat})~${nearBy}`;
                        url = url + notStr;
                    }
                });
            } else {
                activeOperators.val.forEach((item, index) => {
                    if (item === 'not') {
                        notStr = `,-(${values.val[index]})`;
                        url = url + notStr;
                    }

                    if (item === 'or') {
                        orStr = ` or (${values.val[index]})`;
                        url = url + orStr;
                    }

                    if (item === 'and') {
                        andStr = `,(${values.val[index]})`;
                        url = url + andStr;
                    }
                });
            }
            setHasQuery(true);
        }

        url =
            url +
            `&page_size=${pageSize}&include_data=true&search_fields=${searchField}&page=${activePage}&highlights=true&include_aggs=author,assettype,language,source,collection_acip,collection_ramachandra`;

        //Update URL parameter
        if (
            authorFilter.length > 0 ||
            languageFilter.length > 0 ||
            assetsFilter.length > 0 ||
            filterTree.length > 0 ||
            sourceFilter.length > 0 ||
            collectionFilter.length > 0 ||
            collectionRamachandraFilter.length > 0
        ) {
            url = url + `&filter=`;
        }

        //Update URL for language filter
        if (languageFilter.length > 0) {
            url = url + languageFilter.map((filter) => `${filter}`).join(',');
        }

        //Update URL for assets filter
        if (assetsFilter.length > 0) {
            if (languageFilter.length > 0) {
                url = url + `,`;
            }
            if (
                assetsFilter.length === 1 &&
                assetsFilter.indexOf('items:exists') !== -1
            ) {
                url = url + 'items:-exists';
            } else {
                url = url + assetsFilter.map((item) => `${item}`).join(',');
            }
        }

        //Update URL for author filter
        if (authorFilter.length > 0) {
            if (languageFilter.length > 0 || assetsFilter.length > 0) {
                url = url + `,`;
            }
            url =
                url +
                `${authorFilter.map((item) => `author:(${item})`).join(',')}`;
        }

        //Updating URL for Source Libraries
        if (sourceFilter) {
            if (
                languageFilter.length > 0 ||
                assetsFilter.length ||
                authorFilter.length > 0
            ) {
                url = url + `,`;
            }
            url = url + `source:${sourceFilter}`;
        }

        //Updating URL for collections
        if (collectionFilter) {
            if (
                languageFilter.length > 0 ||
                authorFilter.length > 0 ||
                assetsFilter.length ||
                sourceFilter.length > 0
            ) {
                url = url + `,`;
            }
            url = url + `collection_acip:${collectionFilter}`;
        }
        //Updating URL for collections  Ramachandra
        if (collectionRamachandraFilter.length > 0) {
            if (
                languageFilter.length > 0 ||
                authorFilter.length > 0 ||
                assetsFilter.length ||
                sourceFilter.length > 0 ||
                collectionFilter.length > 0
            ) {
                url = url + `,`;
            }
            url = url + `collection_ramachandra:${collectionRamachandraFilter}`;
        }

        //Updating URL for Subject tree
        if (filterTree.length > 0) {
            if (
                languageFilter.length > 0 ||
                authorFilter.length > 0 ||
                assetsFilter.length ||
                sourceFilter.length > 0 ||
                collectionFilter.length > 0 ||
                collectionRamachandraFilter.length > 0
            ) {
                url = url + `,`;
            }

            url =
                url +
                `subjecttree${treeLanguage === 'en' ? '_eng' : ''}:${filterTree
                    .map((item) => `${item}`)
                    .join('/')}`;
        }

        //Appending value to url on blank search
        if (searchQuery === '') {
            url = url + `&class=works`;
        }
        const resp = await fetch(url, {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json'
            }
        });
        const json = await resp.json();
        setData(json?.hits || []);
        setAggregations(json?.aggregations || []);

        setTotalRecord(json?.total?.value || 0);
        setFetchURL(url);
        setIsLoading(false);

        // Update cookie
        if (
            searchQuery.length > 0 &&
            (localStorage.getItem('recentSearch1') !== searchQuery ||
                localStorage.getItem('recentSearch1') === '')
        ) {
            let item1 = localStorage.getItem('recentSearch1');
            let item2 = localStorage.getItem('recentSearch2');
            let item3 = localStorage.getItem('recentSearch3');
            localStorage.setItem('recentSearch3', item2 ? item2 : '');
            localStorage.setItem('recentSearch2', item1 ? item1 : '');
            localStorage.setItem('recentSearch1', searchQuery);
            setRecentSearch([item1, item2, item3]);
        }

        //Scroll to top
        if (document.querySelector('.search-bar')) {
            document.querySelector('.search-bar').scrollIntoView({
                behavior: 'smooth',
                block: 'end',
                inline: 'nearest'
            });
        }
    }

    // Fetch data filter change
    useEffect(() => {
        if (
            filterTree.length > 0 ||
            values.val.length > 0 ||
            authorFilter.length > 0 ||
            assetsFilter.length > 0 ||
            data.length > 0
        ) {
            fetchData();
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [
        languageFilter,
        assetsFilter,
        searchField,
        authorFilter,
        sourceFilter,
        collectionFilter,
        collectionRamachandraFilter
    ]);

    // Fetch data on page change
    useEffect(() => {
        if (init) {
            fetchData();
        }

        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [activePage]);

    // Get single view
    async function getSingleView() {
        if (isLoading || !single) {
            return;
        }

        setIsLoading(true);

        let url = `https://api.asianlegacylibrary.org/resources/${single}/search/v2/?q=`;
        if (searchQuery) {
            if (activeOperators.val.indexOf('or') !== -1) {
                url = url + `(${searchQuery}`;
            } else {
                url = url + searchQuery;
            }
        }

        if (values.val.length > 0 && activeOperators.val.length > 0) {
            let notStr;
            let orStr;
            let andStr;
            if (searchQuery && activeOperators.val.indexOf('or') === -1) {
                url = url + ',';
            }
            if (activeOperators.val.indexOf('near') !== -1) {
                activeOperators.val.forEach((item, index) => {
                    if (item === 'near') {
                        notStr = `near(${searchQuery}:${nearThat})~${nearBy}`;
                        url = url + notStr;
                    }
                });
            } else {
                activeOperators.val.forEach((item, index) => {
                    if (item === 'not') {
                        notStr = `-(${values.val[index]})`;
                        url = url + notStr;
                    }

                    if (item === 'or') {
                        orStr = ` or (${values.val[index]})`;
                        url = url + orStr;
                        if (searchQuery) {
                            url = url + `)`;
                        }
                    }

                    if (item === 'and') {
                        andStr = `(${values.val[index]})`;
                        url = url + andStr;
                    }
                });
            }
        }
        if (globalSearch) {
            if (searchQuery) {
                url = url + ',';
            }
            url = url + globalSearch;
        }
        const resp = await fetch(url, {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json'
            }
        });
        console.log(url);
        if (resp.status === 200) {
            const json = await resp.json();

            setRelatedResources([]);
            setSingleData(json?.hits || []);
        } else {
            setIsLoading(false);
            return;
        }

        if (allResources.length === 0) {
            //Fetch 200 records for navigation
            if (fetchURL) {
                url = fetchURL.replace('page_size=10', 'page_size=200');
                url = url.replace('highlights=true', 'highlights=false');
                url = url.replace(
                    `page=${activePage}`,
                    `page=${activePage > 20 ? Math.ceil(activePage / 20) : 1}`
                );
                url = url.replace('include_data=true', 'include_data=false');
            } else {
                // console.log('else');
                url = `https://api.asianlegacylibrary.org/resources?q=&page_size=200`;
            }
            // console.log('made call here', url);
            const resp1 = await fetch(url, {
                method: 'GET',
                headers: {
                    'Content-Type': 'application/json'
                }
            });
            const json1 = await resp1.json();
            if (json1?.hits?.length !== 0) {
                let arr = [];
                json1?.hits?.forEach((item, index) => arr.push(item?._id));
                setAllResources(arr);
            }
        }

        setIsLoading(false);
    }
    useEffect(() => {
        if (single) {
            getSingleView();
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [single]);

    // Get Source Libraries
    useEffect(() => {
        async function getSourceLibraries() {
            let url = `https://api.asianlegacylibrary.org/resources/summarize?agg_field=source`;

            const resp = await fetch(url, {
                method: 'GET',
                headers: {
                    'Content-Type': 'application/json'
                }
            });
            const json = await resp.json();
            setSourceLibraries(json?.TermsListing || []);
        }
        if (sourceLibraries.length === 0) {
            getSourceLibraries();
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    // Get Collections
    useEffect(() => {
        async function getCollections() {
            let url = `https://api.asianlegacylibrary.org/resources/summarize?agg_field=collection_acip`;

            const resp = await fetch(url, {
                method: 'GET',
                headers: {
                    'Content-Type': 'application/json'
                }
            });
            const json = await resp.json();
            setCollections(json?.TermsListing || []);
        }
        if (collections.length === 0) {
            getCollections();
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    return (
        <APIContext.Provider
            value={{
                data,
                isLoading,
                searchQuery,
                totalRecord,
                activePage,
                pageSize,
                recentSearch,
                languageFilter,
                single,
                singleData,
                assetsFilter,
                showAdvanceSearch,
                subjectTree,
                filterTree,
                activeOperators,
                values,
                relatedResources,
                treeLanguage,
                sourceLibraries,
                collections,
                searchField,
                globalSearch,
                allResources,
                aggregations,
                // nearThis,
                // setNearThis,
                nearThat,
                setNearThat,
                nearBy,
                setNearBy,
                setAuthorFilter,
                authorFilter,
                setAggregations,
                setGlobalSearch,
                setAllResources,
                setSearchField,

                setSourceLibraries,
                setTreeLanguage,
                setRelatedResources,
                setValues,
                setActiveOperators,
                setFilterTree,
                setShowAdvanceSearch,
                setAssetsFilter,
                setSingleData,
                setSingle,
                setLanguageFilter,
                setIsLoading,
                setSearchQuery,
                setActivePage,
                fetchData,
                setData,
                init,
                setInit,
                fetchURL,
                setSubjectTree,
                sourceFilter,
                setSourceFilter,
                setHasQuery,
                hasQuery,
                setCollectionFilter,
                collectionFilter,
                getSingleView,
                collectionRamachandraFilter,
                setCollectionRamachandraFilter
            }}
        >
            {children}
        </APIContext.Provider>
    );
}

export default APIContextProvider;

export function useAPI() {
    const context = useContext(APIContext);
    if (context === undefined) {
        throw new Error('Context must be used within a Provider');
    }
    return context;
}
