import { useEffect } from 'react';
import { useAPI } from '../../context/apiContext';
import { Link } from 'react-router-dom';

const RelatedResources = ({ relatedworks = [] }) => {
    const {
        setIsLoading,
        relatedResources,
        setRelatedResources,
        singleData,
        single,
        setSingle
    } = useAPI();

    useEffect(() => {
        async function getResource(id) {
            let url = `https://api.asianlegacylibrary.org/resources/${id}?include_data=true`;

            const resp = await fetch(url, {
                method: 'GET',
                headers: {
                    'Content-Type': 'application/json'
                }
            });
            const json = await resp.json();

            setRelatedResources(json?.hits || []);

            setIsLoading(false);
        }
        if (relatedworks.length && singleData.length > 0) {
            relatedworks.map((item) =>
                getResource(
                    item?.['bibframe:translation@work']
                        ?.replaceAll('<em>', '')
                        ?.replaceAll('</em>', '') ||
                        item?.['bibframe:translationof@work']
                            ?.replaceAll('<em>', '')
                            ?.replaceAll('</em>', '')
                )
            );
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [singleData, single]);

    return (
        <>
            <div className="px-4 mx-auto mt-12 mb-5 max-w-7xl">
                <p className="uppercase md:text-xl mb-7">RELATED RESOURCES</p>
                <div className="grid gap-10 lg:grid-cols-3">
                    {relatedResources &&
                        relatedResources.map((item, index) => (
                            <Link
                                to={`/resource/${item?._id}`}
                                className="block cursor-pointer search-result-item group"
                                key={index}
                                onClick={(e) => setSingle('')}
                            >
                                {item?._source['bibframe:person'] && (
                                    <h5
                                        className="text-sm"
                                        dangerouslySetInnerHTML={{
                                            __html:
                                                item?._source?.[
                                                    'bibframe:person'
                                                ] || '--'
                                        }}
                                    />
                                )}
                                {item?._source?.['bibframe:maintitle'] && (
                                    <p
                                        className="block mb-1 text-xl leading-9 text-black hover:underline"
                                        dangerouslySetInnerHTML={{
                                            __html:
                                                item?.highlight?.[
                                                    'bibframe:maintitle'
                                                ] ||
                                                item?._source?.[
                                                    'bibframe:maintitle'
                                                ]
                                        }}
                                    />
                                )}
                                {item?._source?.[
                                    'bibframe:varianttitle@colophon'
                                ] && (
                                    <p
                                        className="mb-5 text-base leading-8 text-gray-500"
                                        dangerouslySetInnerHTML={{
                                            __html:
                                                item?.highlight?.[
                                                    'bibframe:varianttitle@colophon'
                                                ] ||
                                                item?._source?.[
                                                    'bibframe:varianttitle@colophon'
                                                ]
                                        }}
                                    />
                                )}
                            </Link>
                        ))}
                </div>
            </div>
        </>
    );
};
export default RelatedResources;
