import { Link } from 'react-router-dom';
import { useAPI } from '../../context/apiContext';
function cleanChar(val) {
    if (!val) return;
    return String(val)
        .replaceAll(/^(@\w+)\s/g, '<br/><strong>$1</strong> ')
        .replaceAll(/\s(@\w+)\s/g, '<br/><strong>$1</strong><br/>');
    // .replaceAll(/\[\/n\]/gi, '');
}

const SearchItem = ({ item, index }) => {
    const { setSingle } = useAPI();
    return (
        <Link
            to={`/resource/${item?._id}`}
            className="block cursor-pointer search-result-item group"
            key={index}
            onClick={(e) => setSingle('')}
        >
            <div className="grid grid-cols-12 mb-2.5 ">
                <div className="col-span-2 md:col-span-1">
                    <p className="mb-0 text-xs text-gray-500 md:text-sm">
                        Author
                    </p>
                </div>
                <div className="col-span-10 md:col-span-11">
                    <p
                        className="mb-0 text-xs text-black sm:text-sm"
                        dangerouslySetInnerHTML={{
                            __html:
                                item?.highlight?.['bibframe:person'] ||
                                item?._source?.['bibframe:person'] ||
                                '--'
                        }}
                    />
                </div>
            </div>
            <div className="grid grid-cols-12">
                <div className="col-span-2 md:col-span-1">
                    <p className="mb-0 text-xs text-gray-500 md:text-sm">
                        Title
                    </p>
                </div>
                <div className="col-span-10 md:col-span-11">
                    <p
                        className="block mb-1 text-base text-black whitespace-pre-line md:text-xl md:leading-9 hover:underline group-hover:underline"
                        dangerouslySetInnerHTML={{
                            __html: cleanChar(
                                item?.highlight?.['bibframe:maintitle'] ||
                                    item?._source?.['bibframe:maintitle'] ||
                                    '--'
                            )
                        }}
                    />

                    <p
                        className="mb-0 text-xs text-gray-500 whitespace-pre-line md:leading-8 md:text-base"
                        dangerouslySetInnerHTML={{
                            __html: cleanChar(
                                item?.highlight?.[
                                    'bibframe:varianttitle@colophon'
                                ] ||
                                    item?._source?.[
                                        'bibframe:varianttitle@colophon'
                                    ]
                            )
                        }}
                    />

                    <div className="items-center mt-5 space-y-2 sm:divide-x sm:space-x-3 sm:space-y-0 sm:flex sm:divide-gray">
                        <p className="m-0 text-xs leading-none text-gray">
                            Source Library:{' '}
                            {item?._source?.['bibframe:source'] || '--'}
                        </p>
                        <p className="m-0 text-xs leading-none sm:pl-3 text-gray">
                            Language:{' '}
                            {item?._source?.['bibframe:language'] || '--'}
                        </p>
                        <p className="m-0 text-xs leading-none sm:pl-3 text-gray">
                            Asset Type:{' '}
                            {item?._source?.['all:items']?.['all:assettype'] ||
                                '--'}
                        </p>
                    </div>
                </div>
            </div>
        </Link>
    );
};
export default SearchItem;
