import React, { useEffect, useState } from 'react';
import { useAPI } from '../../context/apiContext';

const SubjectTree = ({
    branch1,
    branch2,
    branch3,
    branch4,
    branch5,
    filterTreefn
}) => {
    const { subjectTree, setFilterTree, treeLanguage } = useAPI();
    const [active, setActive] = useState('');

    // States for subject tree branch
    const [firstBranch, setfirstBranch] = useState({
        open: false,
        data: '',
        acitve: ''
    });
    const [secondBranch, setSecondBranch] = useState({
        open: false,
        data: '',
        acitve: ''
    });
    const [thirdBranch, setThirdBranch] = useState({
        open: false,
        data: '',
        acitve: ''
    });
    const [fourthBranch, setFourthBranch] = useState({
        open: false,
        data: '',
        acitve: ''
    });
    // reseting states on init
    useEffect(() => {
        setfirstBranch({
            open: false,
            data: '',
            acitve: ''
        });
        setSecondBranch({
            open: false,
            data: '',
            acitve: ''
        });
        setThirdBranch({
            open: false,
            data: '',
            acitve: ''
        });
        setFourthBranch({
            open: false,
            data: '',
            acitve: ''
        });
        setFilterTree([]);
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [treeLanguage]);

    function scrollHoriz() {
        setTimeout(() => {
            document.querySelector('.subject-tree').scroll({
                top: 0,
                left: 500,
                behavior: 'smooth'
            });
        }, 100);
    }

    // function filterTreefn(slug) {
    //     const index = filterTree.indexOf(slug);

    //     if (index === -1) {
    //         setFilterTree([...filterTree, slug]);
    //     } else {
    //         const copy = [...filterTree];
    //         copy.splice(index, 1);
    //         setFilterTree(copy);
    //     }
    // }

    return (
        <div className="relative flex overflow-auto bg-white h-96 subject-tree">
            <div className="w-1/2 space-y-4 sm:w-1/4 sm:min-w-1/4 min-w-1/2">
                {subjectTree &&
                    Object.keys(subjectTree).map((item, index) => (
                        <>
                            <label
                                htmlFor={`0` + item}
                                key={index}
                                className="flex items-center justify-between cursor-pointer"
                            >
                                <div
                                    key={index}
                                    className="relative flex items-center group"
                                >
                                    <input
                                        type="checkbox"
                                        id={`0` + item}
                                        onChange={(e) => {
                                            filterTreefn(item, 1);
                                            setFourthBranch({
                                                open: false,
                                                data: {},
                                                acitve: ''
                                            });
                                            setThirdBranch({
                                                open: false,
                                                data: {},
                                                acitve: ''
                                            });
                                            setSecondBranch({
                                                open: false,
                                                data: {},
                                                acitve: ''
                                            });
                                            setfirstBranch({
                                                open: false,
                                                data: {},
                                                acitve: ''
                                            });
                                            setfirstBranch({
                                                open: e.target.checked,
                                                data: subjectTree[item]
                                            });
                                            setActive(item);
                                            document
                                                .querySelector('.subject-tree')
                                                .scroll({
                                                    top: 0,
                                                    behavior: 'smooth'
                                                });
                                            scrollHoriz();
                                        }}
                                        checked={
                                            branch1 === item ? true : false
                                        }
                                        className="w-3 h-3 mr-3 text-black border-2 border-gray-400 border-solid form-checkbox"
                                    />
                                    <span className="absolute z-10 w-full max-w-full px-2 py-1 text-xs font-light text-white transform -translate-x-1/2 rounded opacity-0 pointer-events-none top-6 left-1/2 bg-green group-hover:opacity-100">
                                        {item}
                                    </span>
                                    <span
                                        className={`text-sm block whitespace-nowrap overflow-hidden overflow-ellipsis w-24  ${
                                            active !== item
                                                ? 'text-gray-500'
                                                : 'text-black'
                                        } `}
                                    >
                                        {item}
                                    </span>
                                </div>
                                {typeof subjectTree[item] === 'object' && (
                                    <img
                                        src={`${process.env.REACT_APP_DOMAIN}wp-content/plugins/asian-legacy-library-plugin/library-plugin-images/angle.svg`}
                                        className="opacity-50"
                                        alt=""
                                    />
                                )}
                            </label>
                        </>
                    ))}
            </div>

            <div
                className={` w-1/2 space-y-4  sm:w-1/4 sm:min-w-1/4 min-w-1/2 pl-4 ${
                    firstBranch.open ? '' : 'hidden'
                }`}
            >
                {firstBranch.data &&
                    Object.keys(firstBranch.data).map((item, i) =>
                        item ? (
                            <label
                                htmlFor={`2` + item}
                                key={i}
                                className="flex items-center justify-between cursor-pointer"
                            >
                                <div className="relative flex items-center group">
                                    <input
                                        onChange={(e) => {
                                            filterTreefn(item, 2);
                                            setFourthBranch({
                                                open: false,
                                                data: {},
                                                acitve: ''
                                            });
                                            setThirdBranch({
                                                open: false,
                                                data: {},
                                                acitve: ''
                                            });
                                            setSecondBranch({
                                                open: false,
                                                data: {},
                                                acitve: ''
                                            });
                                            setSecondBranch({
                                                open: e.target.checked,
                                                data: firstBranch.data[item],
                                                active: item
                                            });
                                            if (
                                                typeof firstBranch.data[
                                                    item
                                                ] === 'object'
                                            ) {
                                                document
                                                    .querySelector(
                                                        '.subject-tree'
                                                    )
                                                    .scroll({
                                                        top: 0,
                                                        behavior: 'smooth'
                                                    });
                                                scrollHoriz();
                                            }
                                        }}
                                        id={`2` + item}
                                        type="checkbox"
                                        checked={
                                            branch2 === item ? true : false
                                        }
                                        className="w-3 h-3 mr-3 text-black border-2 border-gray-400 border-solid form-checkbox"
                                    />{' '}
                                    <span className="absolute z-10 w-full max-w-full px-2 py-1 text-xs font-light text-white transform -translate-x-1/2 rounded opacity-0 pointer-events-none top-6 left-1/2 bg-green group-hover:opacity-100">
                                        {item}
                                    </span>
                                    <span
                                        className={`text-sm block whitespace-nowrap overflow-hidden overflow-ellipsis w-24 ${
                                            secondBranch?.active !== item
                                                ? 'text-gray-500'
                                                : 'text-black'
                                        } `}
                                    >
                                        {item}
                                    </span>
                                </div>
                                {typeof firstBranch.data[item] === 'object' && (
                                    <img
                                        src={`${process.env.REACT_APP_DOMAIN}wp-content/plugins/asian-legacy-library-plugin/library-plugin-images/angle.svg`}
                                        className="opacity-50"
                                        alt=""
                                    />
                                )}
                            </label>
                        ) : (
                            ''
                        )
                    )}
            </div>
            <div
                className={` w-1/2 space-y-4  sm:w-1/4 sm:min-w-1/4 min-w-1/2 pl-4 ${
                    secondBranch.open ? '' : 'hidden'
                }`}
            >
                {secondBranch.data &&
                    Object.keys(secondBranch.data).map((item, i) =>
                        item ? (
                            <label
                                htmlFor={`3` + item}
                                key={i}
                                className="flex items-center justify-between cursor-pointer "
                            >
                                <div
                                    key={i}
                                    className="relative flex items-center group"
                                >
                                    <input
                                        id={`3` + item}
                                        onChange={(e) => {
                                            filterTreefn(item, 3);
                                            setFourthBranch({
                                                open: false,
                                                data: {},
                                                acitve: ''
                                            });
                                            setThirdBranch({
                                                open: false,
                                                data: {},
                                                acitve: ''
                                            });
                                            if (
                                                typeof secondBranch.data[
                                                    item
                                                ] === 'object'
                                            ) {
                                                setThirdBranch({
                                                    open: e.target.checked,
                                                    data: secondBranch.data[
                                                        item
                                                    ],
                                                    active: item
                                                });
                                                document
                                                    .querySelector(
                                                        '.subject-tree'
                                                    )
                                                    .scroll({
                                                        top: 0,
                                                        behavior: 'smooth'
                                                    });
                                                scrollHoriz();
                                            }
                                        }}
                                        type="checkbox"
                                        checked={
                                            branch3 === item ? true : false
                                        }
                                        className="w-3 h-3 mr-3 text-black border-2 border-gray-400 border-solid form-checkbox"
                                    />{' '}
                                    <span className="absolute z-10 w-full max-w-full px-2 py-1 text-xs font-light text-white transform -translate-x-1/2 rounded opacity-0 pointer-events-none top-6 left-1/2 bg-green group-hover:opacity-100">
                                        {item}
                                    </span>
                                    <span
                                        className={`text-sm block whitespace-nowrap overflow-hidden overflow-ellipsis w-24 ${
                                            thirdBranch?.active !== item
                                                ? 'text-gray-500'
                                                : 'text-black'
                                        } `}
                                    >
                                        {item}
                                    </span>
                                </div>

                                {typeof secondBranch.data[item] ===
                                    'object' && (
                                    <img
                                        src={`${process.env.REACT_APP_DOMAIN}wp-content/plugins/asian-legacy-library-plugin/library-plugin-images/angle.svg`}
                                        className="opacity-50"
                                        alt=""
                                    />
                                )}
                            </label>
                        ) : (
                            ''
                        )
                    )}
            </div>
            <div
                className={`w-1/2 space-y-4  sm:w-1/4 sm:min-w-1/4 min-w-1/2 pl-4 ${
                    thirdBranch.open ? '' : 'hidden'
                }`}
            >
                {thirdBranch.data &&
                    Object.keys(thirdBranch.data).map((item, i) =>
                        item ? (
                            <label
                                htmlFor={`4` + item}
                                key={i}
                                className="flex items-center justify-between cursor-pointer"
                            >
                                <div
                                    key={i}
                                    className="relative flex items-center group"
                                >
                                    <input
                                        id={`4` + item}
                                        type="checkbox"
                                        checked={
                                            branch4 === item ? true : false
                                        }
                                        onChange={(e) => {
                                            filterTreefn(item, 4);
                                            setFourthBranch({
                                                open: false,
                                                data: {},
                                                acitve: ''
                                            });
                                            if (
                                                typeof thirdBranch.data[
                                                    item
                                                ] === 'object'
                                            ) {
                                                setFourthBranch({
                                                    open: e.target.checked,
                                                    data: thirdBranch.data[
                                                        item
                                                    ],
                                                    active: item
                                                });
                                                scrollHoriz();
                                            }
                                        }}
                                        className="w-3 h-3 mr-3 text-black border-2 border-gray-400 border-solid form-checkbox"
                                    />{' '}
                                    <span className="absolute z-10 w-full max-w-full px-2 py-1 text-xs font-light text-white transform -translate-x-1/2 rounded opacity-0 pointer-events-none top-6 left-1/2 bg-green group-hover:opacity-100">
                                        {item}
                                    </span>
                                    <span className="block w-24 overflow-hidden text-sm text-gray-500 whitespace-nowrap overflow-ellipsis">
                                        {item}
                                    </span>
                                </div>

                                {typeof thirdBranch.data[item] === 'object' && (
                                    <img
                                        src={`${process.env.REACT_APP_DOMAIN}wp-content/plugins/asian-legacy-library-plugin/library-plugin-images/angle.svg`}
                                        className="opacity-50"
                                        alt=""
                                    />
                                )}
                            </label>
                        ) : (
                            ''
                        )
                    )}
            </div>
            <div
                className={`w-1/2 space-y-4  sm:w-1/4 sm:min-w-1/4 min-w-1/2 pl-4 ${
                    fourthBranch.open ? '' : 'hidden'
                }`}
            >
                {fourthBranch.data &&
                    Object.keys(fourthBranch.data).map((item, i) => (
                        <label
                            htmlFor={`5` + item}
                            key={i}
                            className="flex items-center justify-between cursor-pointer"
                        >
                            <div
                                key={i}
                                className="relative flex items-center group"
                            >
                                <input
                                    id={`5` + item}
                                    type="checkbox"
                                    checked={branch5 === item ? true : false}
                                    onChange={(e) => {
                                        filterTreefn(item, 5);
                                    }}
                                    className="w-3 h-3 mr-3 text-black border-2 border-gray-400 border-solid form-checkbox"
                                />{' '}
                                <span className="absolute z-10 w-full max-w-full px-2 py-1 text-xs font-light text-white transform -translate-x-1/2 rounded opacity-0 pointer-events-none top-6 left-1/2 bg-green group-hover:opacity-100">
                                    {item}
                                </span>
                                <span className="block w-24 overflow-hidden text-sm text-gray-500 whitespace-nowrap overflow-ellipsis">
                                    {item}
                                </span>
                            </div>
                        </label>
                    ))}
            </div>
        </div>
    );
};
export default SubjectTree;
