import { useState } from 'react';
import { useAPI } from '../../context/apiContext';

const Filters = () => {
    const {
        aggregations,
        languageFilter,
        setLanguageFilter,
        assetsFilter,
        setAssetsFilter,
        isLoading,
        setAuthorFilter,
        authorFilter,
        sourceFilter,
        setSourceFilter,
        setActivePage,
        setCollectionFilter,
        collectionFilter,
        setCollectionRamachandraFilter,
        collectionRamachandraFilter
    } = useAPI();

    const {
        author,
        assettype,
        language,
        source,
        collection_acip = [],
        collection_ramachandra = []
    } = aggregations;

    //Filter languages
    function filterLanguage(slug) {
        const index = languageFilter.indexOf(slug);

        if (index === -1) {
            setLanguageFilter([slug]);
        } else {
            setLanguageFilter([]);
        }
        setActivePage(1);
    }

    //Fitler assets
    function filterAssets(slug) {
        const index = assetsFilter.indexOf(slug);

        if (index === -1) {
            setAssetsFilter([...assetsFilter, slug]);
        } else {
            const copy = [...assetsFilter];
            copy.splice(index, 1);
            setAssetsFilter(copy);
        }
        setActivePage(1);
    }
    //Fitler author
    function filterAuthor(slug) {
        const index = authorFilter.indexOf(slug);

        if (index === -1) {
            setAuthorFilter([...authorFilter, slug]);
        } else {
            const copy = [...authorFilter];
            copy.splice(index, 1);
            setAuthorFilter(copy);
        }
        setActivePage(1);
    }
    //Fitler source
    function filterSource(slug) {
        const index = sourceFilter.indexOf(slug);

        if (index === -1) {
            setSourceFilter(slug);
        } else {
            setSourceFilter('');
        }
        setActivePage(1);
    }
    //Fitler Collections
    function filterCollection(slug) {
        const index = collectionFilter.indexOf(slug);

        if (index === -1) {
            setCollectionFilter(slug);
        } else {
            setCollectionFilter('');
        }
        setActivePage(1);
    }
    //Fitler Ramachandra
    function filterCollectionRamachandra(slug) {
        const index = collectionRamachandraFilter.indexOf(slug);

        if (index === -1) {
            setCollectionRamachandraFilter(slug);
        } else {
            setCollectionRamachandraFilter('');
        }
        setActivePage(1);
    }
    const [showFilter, setShowFilter] = useState(false);
    const [showAssets, setShowAssets] = useState(true);
    const [showLangugae, setShowLangugae] = useState(true);
    const [showSourceLib, setShowSourceLib] = useState(true);
    const [showCollection, setShowCollection] = useState(true);
    const [showCollectionRamachandra, setShowCollectionRamachandra] =
        useState(true);

    const [ShowAuthor, setShowAuthor] = useState(true);
    return (
        <>
            <button
                type="button"
                className="inline-flex items-center md:hidden"
                onClick={(e) => setShowFilter(!showFilter)}
            >
                <img
                    src={`${process.env.REACT_APP_DOMAIN}wp-content/plugins/asian-legacy-library-plugin/library-plugin-images/filter.svg`}
                    className="w-4 mr-2"
                    alt=""
                />{' '}
                Filter
            </button>

            <div
                className={`w-full md:block md:static fixed md:h-auto h-screen z-999999 md:z-0 left-0 top-0 overflow-auto max-w-260 bg-white   md:p-0 p-4 md:shadow-none shadow md:border-none bprder border-t border-gray-200 ${
                    showFilter ? '' : 'hidden'
                } ${isLoading ? 'md:opacity-60 pointer-events-none' : ''}`}
            >
                <div className="text-right md:hidden">
                    <button
                        type="button"
                        className="inline-flex items-center text-sm md:hidden"
                        onClick={(e) => setShowFilter(!showFilter)}
                    >
                        <img
                            src={`${process.env.REACT_APP_DOMAIN}wp-content/plugins/asian-legacy-library-plugin/library-plugin-images/close.svg`}
                            className="w-3 mr-2"
                            alt=""
                        />{' '}
                        Close
                    </button>
                </div>
                <div className="space-y-10">
                    <div>
                        <h3 className="pl-0 mb-6 text-lg font-normal text-black">
                            FILTER BY:
                        </h3>
                        {assettype?.buckets?.length > 0 ? (
                            <>
                                <p
                                    onClick={(e) => setShowAssets(!showAssets)}
                                    className="flex items-center justify-between mb-5 text-sm text-gray-500 cursor-pointer"
                                >
                                    Asset Types{' '}
                                    <img
                                        className="w-3 md:mr-20"
                                        src={
                                            !showAssets
                                                ? `${process.env.REACT_APP_DOMAIN}wp-content/plugins/asian-legacy-library-plugin/library-plugin-images/plus.svg}`
                                                : `${process.env.REACT_APP_DOMAIN}wp-content/plugins/asian-legacy-library-plugin/library-plugin-images/minus.svg}`
                                        }
                                        alt=""
                                    />
                                </p>
                                <div
                                    className={`flex flex-col space-y-4 ${
                                        !showAssets ? 'hidden' : ''
                                    }`}
                                >
                                    {assettype?.buckets &&
                                        assettype?.buckets.map(
                                            (item, index) => (
                                                <div
                                                    className="flex items-center "
                                                    key={index}
                                                >
                                                    <input
                                                        onChange={(e) => {
                                                            filterAssets(
                                                                `assettype:${item?.key}`
                                                            );
                                                        }}
                                                        checked={
                                                            assetsFilter.indexOf(
                                                                `assettype:${item?.key}`
                                                            ) !== -1
                                                                ? true
                                                                : false
                                                        }
                                                        value={`assettype:${item?.key}`}
                                                        type="checkbox"
                                                        id={item?.key}
                                                        className="w-3 h-3 mr-3 text-black border-2 border-gray-400 border-solid form-checkbox"
                                                    />
                                                    <label
                                                        htmlFor={item?.key}
                                                        className="text-base text-gray-500 capitalize"
                                                    >
                                                        {item?.key} (
                                                        {Number(
                                                            item?.doc_count
                                                        ).toLocaleString()}
                                                        )
                                                    </label>
                                                </div>
                                            )
                                        )}
                                </div>
                            </>
                        ) : (
                            ''
                        )}
                    </div>
                    {language?.buckets?.length > 0 ? (
                        <div>
                            <p
                                onClick={(e) => setShowLangugae(!showLangugae)}
                                className="flex items-center justify-between mb-5 text-sm text-gray-500 cursor-pointer"
                            >
                                Language{' '}
                                <img
                                    className="w-3 md:mr-20"
                                    src={
                                        !showLangugae
                                            ? `${process.env.REACT_APP_DOMAIN}wp-content/plugins/asian-legacy-library-plugin/library-plugin-images/plus.svg}`
                                            : `${process.env.REACT_APP_DOMAIN}wp-content/plugins/asian-legacy-library-plugin/library-plugin-images/minus.svg}`
                                    }
                                    alt=""
                                />
                            </p>
                            <div
                                className={`flex flex-col space-y-4 ${
                                    !showLangugae ? 'hidden' : ''
                                }`}
                            >
                                {language &&
                                    language?.buckets?.map((item, index) => (
                                        <div
                                            className="relative flex items-center group"
                                            key={index}
                                        >
                                            <input
                                                onChange={(e) => {
                                                    filterLanguage(
                                                        `language:${item?.key}`
                                                    );
                                                }}
                                                type="checkbox"
                                                id={item?.key}
                                                name="language"
                                                value={`language:${item?.key}`}
                                                checked={
                                                    languageFilter.indexOf(
                                                        `language:${item?.key}`
                                                    ) !== -1
                                                        ? true
                                                        : false
                                                }
                                                className="w-3 h-3 mr-3 text-black border-2 border-gray-400 border-solid form-checkbox"
                                            />
                                            <span className="absolute z-10 w-auto max-w-full px-2 py-1 text-xs font-light text-white transform rounded opacity-0 pointer-events-none left-5 top-6 bg-green group-hover:opacity-100">
                                                {item?.key}
                                            </span>
                                            <label
                                                htmlFor={item?.key}
                                                className="text-base text-gray-500"
                                            >
                                                {item?.key.substring(0, 12)}
                                                {item?.key.length > 12
                                                    ? '...'
                                                    : ''}{' '}
                                                (
                                                {Number(
                                                    item?.doc_count
                                                ).toLocaleString()}
                                                )
                                            </label>
                                        </div>
                                    ))}
                            </div>
                        </div>
                    ) : (
                        ''
                    )}
                    {source?.buckets?.length > 0 ? (
                        <div>
                            <p
                                onClick={(e) =>
                                    setShowSourceLib(!showSourceLib)
                                }
                                className="flex items-center justify-between mb-5 text-sm text-gray-500 cursor-pointer"
                            >
                                Source Libraries{' '}
                                <img
                                    className="w-3 md:mr-20"
                                    src={
                                        !showSourceLib
                                            ? `${process.env.REACT_APP_DOMAIN}wp-content/plugins/asian-legacy-library-plugin/library-plugin-images/plus.svg}`
                                            : `${process.env.REACT_APP_DOMAIN}wp-content/plugins/asian-legacy-library-plugin/library-plugin-images/minus.svg}`
                                    }
                                    alt=""
                                />
                            </p>
                            <div
                                className={`flex flex-col space-y-4 ${
                                    !showSourceLib ? 'hidden' : ''
                                }`}
                            >
                                {source &&
                                    source?.buckets?.map((item, index) => (
                                        <div
                                            className="relative flex items-center group"
                                            key={index}
                                        >
                                            <input
                                                onChange={(e) => {
                                                    filterSource(
                                                        `${item?.key}`
                                                    );
                                                }}
                                                type="checkbox"
                                                id={item?.key}
                                                name="source"
                                                value={`${item?.key}`}
                                                checked={
                                                    sourceFilter === item?.key
                                                        ? true
                                                        : false
                                                }
                                                className="w-3 h-3 mr-3 text-black border-2 border-gray-400 border-solid form-checkbox"
                                            />
                                            <span className="absolute z-10 w-auto max-w-full px-2 py-1 text-xs font-light text-white transform rounded opacity-0 pointer-events-none left-5 top-6 bg-green group-hover:opacity-100">
                                                {item?.key}
                                            </span>
                                            <label
                                                htmlFor={item?.key}
                                                className="text-base text-gray-500"
                                            >
                                                {item?.key.substring(0, 12)}
                                                {item?.key.length > 12
                                                    ? '...'
                                                    : ''}{' '}
                                                (
                                                {Number(
                                                    item?.doc_count
                                                ).toLocaleString()}
                                                )
                                            </label>
                                        </div>
                                    ))}
                            </div>
                        </div>
                    ) : (
                        ''
                    )}

                    {collection_acip?.buckets?.length > 0 ? (
                        <div>
                            <p
                                onClick={(e) =>
                                    setShowCollection(!showCollection)
                                }
                                className="flex items-center justify-between mb-5 text-sm text-gray-500 cursor-pointer"
                            >
                                Collections
                                <img
                                    className="w-3 md:mr-20"
                                    src={
                                        !showCollection
                                            ? `${process.env.REACT_APP_DOMAIN}wp-content/plugins/asian-legacy-library-plugin/library-plugin-images/plus.svg}`
                                            : `${process.env.REACT_APP_DOMAIN}wp-content/plugins/asian-legacy-library-plugin/library-plugin-images/minus.svg}`
                                    }
                                    alt=""
                                />
                            </p>
                            <div
                                className={`flex flex-col space-y-4 ${
                                    !showCollection ? 'hidden' : ''
                                }`}
                            >
                                {collection_acip &&
                                    collection_acip?.buckets?.map(
                                        (item, index) => (
                                            <div
                                                className="relative flex items-center group"
                                                key={index}
                                            >
                                                <input
                                                    onChange={(e) => {
                                                        filterCollection(
                                                            `${item?.key}`
                                                        );
                                                    }}
                                                    type="checkbox"
                                                    id={item?.key}
                                                    name="source"
                                                    value={`${item?.key}`}
                                                    checked={
                                                        collectionFilter ===
                                                        item?.key
                                                            ? true
                                                            : false
                                                    }
                                                    className="w-3 h-3 mr-3 text-black border-2 border-gray-400 border-solid form-checkbox"
                                                />
                                                <span className="absolute z-10 w-auto max-w-full px-2 py-1 text-xs font-light text-white transform rounded opacity-0 pointer-events-none left-5 top-6 bg-green group-hover:opacity-100">
                                                    {item?.key}
                                                </span>
                                                <label
                                                    htmlFor={item?.key}
                                                    className="text-base text-gray-500"
                                                >
                                                    {item?.key.substring(0, 12)}
                                                    {item?.key.length > 12
                                                        ? '...'
                                                        : ''}{' '}
                                                    (
                                                    {Number(
                                                        item?.doc_count
                                                    ).toLocaleString()}
                                                    )
                                                </label>
                                            </div>
                                        )
                                    )}
                            </div>
                        </div>
                    ) : (
                        ''
                    )}
                    {collection_ramachandra?.buckets?.length > 0 ? (
                        <div>
                            <p
                                onClick={(e) =>
                                    setShowCollectionRamachandra(
                                        !showCollectionRamachandra
                                    )
                                }
                                className="flex items-center justify-between mb-5 text-sm text-gray-500 cursor-pointer"
                            >
                                Collections Ramachandra
                                <img
                                    className="w-3 md:mr-20"
                                    src={
                                        !showCollectionRamachandra
                                            ? `${process.env.REACT_APP_DOMAIN}wp-content/plugins/asian-legacy-library-plugin/library-plugin-images/plus.svg}`
                                            : `${process.env.REACT_APP_DOMAIN}wp-content/plugins/asian-legacy-library-plugin/library-plugin-images/minus.svg}`
                                    }
                                    alt=""
                                />
                            </p>
                            <div
                                className={`flex flex-col space-y-4 ${
                                    !showCollectionRamachandra ? 'hidden' : ''
                                }`}
                            >
                                {collection_ramachandra &&
                                    collection_ramachandra?.buckets?.map(
                                        (item, index) => (
                                            <>
                                                {item?.key && (
                                                    <div
                                                        className="relative flex items-center group"
                                                        key={index}
                                                    >
                                                        <input
                                                            onChange={(e) => {
                                                                filterCollectionRamachandra(
                                                                    `${item?.key}`
                                                                );
                                                            }}
                                                            type="checkbox"
                                                            id={item?.key}
                                                            name="source"
                                                            value={`${item?.key}`}
                                                            checked={
                                                                collectionRamachandraFilter ===
                                                                item?.key
                                                                    ? true
                                                                    : false
                                                            }
                                                            className="w-3 h-3 mr-3 text-black border-2 border-gray-400 border-solid form-checkbox"
                                                        />
                                                        <span className="absolute z-10 w-auto max-w-full px-2 py-1 text-xs font-light text-white transform rounded opacity-0 pointer-events-none left-5 top-6 bg-green group-hover:opacity-100">
                                                            {item?.key}
                                                        </span>
                                                        <label
                                                            htmlFor={item?.key}
                                                            className="text-base text-gray-500"
                                                        >
                                                            {item?.key
                                                                ? item?.key.substring(
                                                                      0,
                                                                      12
                                                                  )
                                                                : '--'}
                                                            {item?.key.length >
                                                            12
                                                                ? '...'
                                                                : ''}{' '}
                                                            (
                                                            {Number(
                                                                item?.doc_count
                                                            ).toLocaleString()}
                                                            )
                                                        </label>
                                                    </div>
                                                )}
                                            </>
                                        )
                                    )}
                            </div>
                        </div>
                    ) : (
                        ''
                    )}
                    {author?.buckets.length > 0 && (
                        <div>
                            <p
                                onClick={(e) => setShowAuthor(!ShowAuthor)}
                                className="flex items-center justify-between mb-5 text-sm text-gray-500 cursor-pointer"
                            >
                                Author{' '}
                                <img
                                    className="w-3 md:mr-20"
                                    src={
                                        !ShowAuthor
                                            ? `${process.env.REACT_APP_DOMAIN}wp-content/plugins/asian-legacy-library-plugin/library-plugin-images/plus.svg}`
                                            : `${process.env.REACT_APP_DOMAIN}wp-content/plugins/asian-legacy-library-plugin/library-plugin-images/minus.svg}`
                                    }
                                    alt=""
                                />
                            </p>
                            <div
                                className={`flex flex-col space-y-4 ${
                                    !ShowAuthor ? 'hidden' : ''
                                }`}
                            >
                                {author?.buckets.map((item, index) => (
                                    <div
                                        className="relative flex items-center group"
                                        key={index}
                                    >
                                        <input
                                            onChange={(e) => {
                                                filterAuthor(item?.key);
                                            }}
                                            type="checkbox"
                                            id={item.key}
                                            name="author"
                                            value={item?.key}
                                            checked={
                                                authorFilter.indexOf(
                                                    item?.key
                                                ) !== -1
                                                    ? true
                                                    : false
                                            }
                                            className="w-3 h-3 mr-3 text-black border-2 border-gray-400 border-solid form-checkbox"
                                        />{' '}
                                        <span className="absolute left-0 z-10 w-48 max-w-full px-2 py-1 text-xs font-light text-white transform rounded opacity-0 pointer-events-none top-6 bg-green group-hover:opacity-100">
                                            {item?.key}
                                        </span>
                                        <label
                                            htmlFor={item?.key}
                                            className="relative block w-40 overflow-hidden text-sm text-gray-500 overflow-ellipsis whitespace-nowrap "
                                        >
                                            {item?.key.substring(0, 12)}
                                            {item?.key.length > 12
                                                ? '...'
                                                : ''}{' '}
                                            (
                                            {Number(
                                                item?.doc_count
                                            ).toLocaleString()}
                                            )
                                        </label>
                                    </div>
                                ))}
                            </div>
                        </div>
                    )}
                </div>
                <h3 className="pl-0 mt-12 mb-2 text-base text-green">
                    HELP US IMPROVE
                </h3>
                <p className="mb-5 text-sm lg:pr-14 text-green">
                    If you have a suggestion to help us improve your search
                    experience,{' '}
                    <a
                        className="underline"
                        href="mailto:librarian@asianlegacylibrary.org  "
                    >
                        let us know.
                    </a>
                </p>
            </div>
        </>
    );
};
export default Filters;
