import { useAPI } from '../../context/apiContext';
const RecentSearch = () => {
    const { recentSearch, setSearchQuery } = useAPI();

    if (recentSearch.length) {
        return (
            <div className="w-full px-4 py-3 mt-3 border border-solid border-yellow">
                <div className="flex flex-col ">
                    <h5 className="block mb-2 text-base text-black">
                        Recent Searches
                    </h5>
                    <ul className="pl-0 m-0 space-y-1 list-none">
                        {recentSearch &&
                            recentSearch.map((item, index) => (
                                <li
                                    key={index}
                                    className="pl-0 m-0 before:hidden"
                                >
                                    <p
                                        onClick={(e) => {
                                            e.stopPropagation();
                                            e.preventDefault();
                                            setSearchQuery(item);
                                        }}
                                        className="block m-0 text-base text-gray-500 cursor-pointer hover:text-black"
                                    >
                                        {item}
                                    </p>
                                </li>
                            ))}
                    </ul>
                </div>
            </div>
        );
    } else {
        return <></>;
    }
};
export default RecentSearch;
