// import { useState } from 'react';
// import { Transition } from '@headlessui/react';
// import angleIcon from '../../images/angle.svg';
import { useAPI } from '../../context/apiContext';
import DataNotFound from './dataNotFound';
import Collections from './collections';
import SearchItem from './searchItem';
import Pagination from './pagination';
import Filters from './filters';
const SearchResult = () => {
    // const [showSorting, setShowSorting] = useState(false);
    const {
        data,
        isLoading,
        searchQuery,
        totalRecord,
        setSingle,

        init,
        authorFilter,
        languageFilter,
        assetsFilter
    } = useAPI();
    return (
        <>
            <div className="justify-center px-5 pt-5 mx-auto md:mt-10 md:pt-0 md:flex">
                {data.length > 0 ||
                ((searchQuery ||
                    authorFilter ||
                    languageFilter ||
                    assetsFilter) &&
                    init) ? (
                    <Filters />
                ) : (
                    ''
                )}

                <div className="w-full max-w-3xl lg:pr-12">
                    {data.length !== 0 && (
                        <div className="mb-10 text-right">
                            <h5 className="text-xl font-normal text-right text-black -mt-7 whitespace-nowrap md:mt-0">
                                {Number(totalRecord).toLocaleString()}
                                {totalRecord === 10000 ? '+' : ''} results
                            </h5>
                        </div>
                    )}
                    <div className="flex flex-col space-y-10">
                        {isLoading && <h5> Searching...</h5>}
                        {data &&
                            data.map((item, index) => (
                                <SearchItem
                                    key={index}
                                    item={item}
                                    index={index}
                                    searchQuery={searchQuery}
                                    setSingle={setSingle}
                                />
                            ))}
                    </div>
                    {data.length === 0 && !isLoading && init && (
                        <DataNotFound />
                    )}
                    {totalRecord > 10 && data.length !== 0 ? (
                        <Pagination />
                    ) : (
                        ''
                    )}
                </div>
            </div>

            {data.length === 0 && !init && <Collections />}
        </>
    );
};
export default SearchResult;
