import { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { useAPI } from '../../context/apiContext';

const Navigation = ({ iiifUrl, size }) => {
    const { allResources, single } = useAPI();
    const [current, setCurrent] = useState(0);

    useEffect(() => {
        setCurrent(allResources.indexOf(single));

        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [single, allResources]);

    return (
        <>
            <div className="p-2 mb-5 text-sm bg-gray-100 md:text-base ">
                <div className="flex items-center justify-between mx-auto max-w-7xl">
                    <Link
                        to={`/resource/${allResources[current - 1]}`}
                        className={
                            current === 0 ||
                            allResources[current - 1] === undefined
                                ? 'pointer-events-none opacity-60 flex items-center'
                                : 'flex items-center'
                        }
                    >
                        <img
                            src={`${process.env.REACT_APP_DOMAIN}wp-content/plugins/asian-legacy-library-plugin/library-plugin-images/ic_expand_more.svg`}
                            className="inline-block w-3 mr-2 transform rotate-90"
                            alt=""
                        />
                        Previous Resource
                    </Link>
                    <Link
                        to={`/resource/${allResources[current + 1]}`}
                        className={
                            allResources.length === 1 ||
                            current === allResources.length - 1
                                ? 'pointer-events-none opacity-60 flex items-center'
                                : 'flex items-center'
                        }
                    >
                        Next Resource
                        <img
                            src={`${process.env.REACT_APP_DOMAIN}wp-content/plugins/asian-legacy-library-plugin/library-plugin-images/ic_expand_more.svg`}
                            className="inline-block w-3 ml-2 transform -rotate-90"
                            alt=""
                        />
                    </Link>
                </div>
            </div>
        </>
    );
};
export default Navigation;
