import { useAPI } from '../../context/apiContext';

const Pagination = () => {
    const { totalRecord, activePage, setActivePage, pageSize, isLoading } =
        useAPI();
    let totalPages = totalRecord / pageSize;
    totalPages = new Array(Math.ceil(totalPages)).fill('');
    let trimPagination;
    if (totalPages.length > 10) {
        trimPagination = true;
    }

    return (
        <div className="py-10 border-t border-gray-300 border-solid mt-14">
            <div
                className={`flex items-center justify-end ${
                    isLoading ? 'opacity-50 pointer-events-none ' : ''
                }`}
            >
                {/* <button
                    onClick={(e) =>
                        setActivePage(activePage !== 1 ? activePage - 1 : {})
                    }
                    className={`py-2.5 px-7 bg-gray-100 text-base hover:bg-gray-200 ${
                        activePage === 1 ? 'pointer-events-none opacity-70' : ''
                    }`}
                >
                    Previous
                </button> */}
                <div className="flex items-center justify-center pr-4 space-x-4">
                    {totalPages.map((item, index) => {
                        if (trimPagination && index + 1 === activePage + 2) {
                            return (
                                <span
                                    key={index}
                                    className="inline-block text-black text-opacity-50 border-b-2 border-black border-opacity-0 px-0.5"
                                >
                                    ...
                                </span>
                            );
                        }
                        if (
                            (trimPagination && index + 1 === 1) ||
                            (trimPagination &&
                                index + 1 === totalPages.length) ||
                            (trimPagination && index + 1 === activePage) ||
                            (trimPagination && index + 1 === activePage - 1) ||
                            (trimPagination && index + 1 === activePage + 1) ||
                            !trimPagination
                        ) {
                            return (
                                <>
                                    <a
                                        key={index}
                                        onClick={(e) => {
                                            e.preventDefault();
                                            setActivePage(index + 1);
                                        }}
                                        href="#!"
                                        className={`inline-block text-black border-b-2 border-black px-0.5 ${
                                            activePage === index + 1
                                                ? 'text-opacity-100  border-solid border-opacity-100'
                                                : 'text-opacity-50  border-solid border-opacity-0'
                                        }`}
                                    >
                                        {index + 1}
                                    </a>
                                </>
                            );
                        }
                        if (trimPagination && index + 1 === activePage - 2) {
                            return (
                                <span
                                    key={index}
                                    className="inline-block text-black text-opacity-50    border-b-2    border-black  border-solid border-opacity-0 px-0.5"
                                >
                                    ...
                                </span>
                            );
                        }
                        return <></>;
                    })}
                </div>
                <a
                    href="#!"
                    onClick={(e) => {
                        e.preventDefault();
                        setActivePage(
                            activePage !== totalPages.length
                                ? activePage + 1
                                : {}
                        );
                    }}
                    className={` mr-4 inline-block ${
                        activePage === totalPages.length
                            ? 'pointer-events-none opacity-70'
                            : ''
                    }`}
                >
                    <img
                        src={`${process.env.REACT_APP_DOMAIN}wp-content/plugins/asian-legacy-library-plugin/library-plugin-images/ic_arrow_forward.svg`}
                        alt=""
                    />
                </a>
                <a
                    href="#!"
                    onClick={(e) => {
                        e.preventDefault();
                        setActivePage(
                            activePage !== totalPages.length
                                ? totalPages.length
                                : {}
                        );
                    }}
                    className={` ${
                        activePage === totalPages.length
                            ? 'pointer-events-none opacity-70'
                            : ''
                    }`}
                >
                    <img
                        src={`${process.env.REACT_APP_DOMAIN}wp-content/plugins/asian-legacy-library-plugin/library-plugin-images/ic_last_page.svg`}
                        alt=""
                    />
                </a>
            </div>
        </div>
    );
};
export default Pagination;
