import { Transition } from '@headlessui/react';
// import Filters from './____filters';
import SubjectTree from './subjectTree';
import QueryBuilder from './queryBuilder';
import RecentSearch from './recent-search';
import { useEffect, useState } from 'react';
import { useAPI } from '../../context/apiContext';

const Search = () => {
    const [showRecentSearch, setShowRecentSearch] = useState(false);
    const [showLanguages, setShowLanguages] = useState(false);
    const [removeClick, setRemoveClick] = useState(false);

    const [branch1, setBranch1] = useState('');
    const [branch2, setBranch2] = useState('');
    const [branch3, setBranch3] = useState('');
    const [branch4, setBranch4] = useState('');
    const [branch5, setBranch5] = useState('');

    const {
        setSearchQuery,
        searchQuery,
        showAdvanceSearch,
        setShowAdvanceSearch,
        fetchData,
        filterTree,
        data,
        setFilterTree,
        activeOperators,
        setActiveOperators,

        setValues,
        setTreeLanguage,
        setSearchField,
        searchField,
        values,
        setData,
        setIsLoading,
        setInit,
        isLoading,
        init,
        setLanguageFilter,
        setAssetsFilter,
        setAuthorFilter,
        authorFilter,
        languageFilter,
        assetsFilter,
        sourceFilter,
        setSourceFilter,
        setActivePage,
        setHasQuery,
        hasQuery,
        activePage,
        collectionFilter,
        setCollectionFilter,
        nearBy,

        collectionRamachandraFilter,
        setCollectionRamachandraFilter
    } = useAPI();

    //Update search based on subject tree filter
    function updateFilters() {
        setActivePage(1);
        if (branch1 && branch2 && branch3 && branch4 && branch5) {
            setFilterTree([branch1, branch2, branch3, branch4, branch5]);
        } else if (branch1 && branch2 && branch3 && branch4) {
            setFilterTree([branch1, branch2, branch3, branch4]);
        } else if (branch1 && branch2 && branch3) {
            setFilterTree([branch1, branch2, branch3]);
        } else if (branch1 && branch2) {
            setFilterTree([branch1, branch2]);
        } else if (branch1) {
            setFilterTree([branch1]);
        } else {
            setFilterTree([]);
        }
        if (activeOperators.val.length && values.val.length) {
            fetchData();
        }
        setRemoveClick(false);
    }

    // Update and clear subject tree
    function filterTreefn(slug, b) {
        if (b === 1) {
            setBranch2('');
            setBranch3('');
            setBranch4('');
            setBranch5('');
            if (branch1 === slug) {
                setBranch1('');
            } else {
                setBranch1(slug);
            }
        } else if (b === 2) {
            setBranch3('');
            setBranch4('');
            setBranch5('');
            if (branch2 === slug) {
                setBranch2('');
            } else {
                setBranch2(slug);
            }
        } else if (b === 3) {
            setBranch4('');
            setBranch5('');
            if (branch3 === slug) {
                setBranch3('');
            } else {
                setBranch3(slug);
            }
        } else if (b === 4) {
            setBranch5('');
            if (branch4 === slug) {
                setBranch4('');
            } else {
                setBranch4(slug);
            }
        } else if (b === 5) {
            if (branch5 === slug) {
                setBranch5('');
            } else {
                setBranch5(slug);
            }
        }
    }

    useEffect(() => {
        if (filterTree.length || init) {
            fetchData();
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [filterTree]);

    useEffect(() => {
        if (removeClick) {
            updateFilters();
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [removeClick]);
    return (
        <>
            <div className="flex flex-col-reverse px-4 md:justify-center md:flex-row">
                <div className="w-32 md:pt-14">
                    {/* Advance filter selection  */}
                    <div className="flex flex-col items-start space-y-0 text-left">
                        <p
                            onClick={() => {
                                setShowAdvanceSearch(-1);
                                setShowLanguages(true);
                                if (!hasQuery) {
                                    setActiveOperators({ val: [] });
                                    setValues({ val: [] });
                                }
                            }}
                            className={`text-sm  block leading-7 pb-1 cursor-pointer mb-0 ${
                                showLanguages ? 'text-black' : 'text-gray-500'
                            }`}
                        >
                            Subject Tree
                        </p>
                        {showLanguages && (
                            <>
                                <p
                                    onClick={() => {
                                        setShowAdvanceSearch(1);
                                        setTreeLanguage('en');
                                    }}
                                    className={`text-xs block leading-5 cursor-pointer pl-2  ${
                                        showLanguages
                                            ? 'text-black'
                                            : 'text-gray-500'
                                    }`}
                                >
                                    English
                                </p>
                                <p
                                    onClick={() => {
                                        setShowAdvanceSearch(1);
                                        setTreeLanguage('bo');
                                    }}
                                    className={`text-xs block leading-5 cursor-pointer pl-2  ${
                                        showLanguages
                                            ? 'text-black'
                                            : 'text-gray-500'
                                    }`}
                                >
                                    Tibetan
                                </p>
                            </>
                        )}
                        <p
                            onClick={() => {
                                setShowAdvanceSearch(2);
                                setShowLanguages(false);

                                if (activeOperators.val.length === 0) {
                                    setValues({ val: [...values.val, ''] });
                                    setActiveOperators({
                                        val: [...activeOperators.val, 'and']
                                    });
                                }
                            }}
                            className={`text-sm  flex items-center leading-7 pb-1 cursor-pointer ${
                                showAdvanceSearch === 2
                                    ? 'text-black pl-2'
                                    : 'text-gray-500'
                            }`}
                        >
                            Query Builder
                        </p>
                    </div>
                </div>
                {/* Main search section including search dropdown  */}
                <div className="block w-full max-w-4xl lg:pr-12 search-bar">
                    <div className="relative ">
                        <div className="relative group">
                            <div className="absolute w-20 pr-1 sm:pr-4 sm:w-40 sm:mt-0 mt-0.5">
                                <select
                                    onChange={(e) => {
                                        e.preventDefault();
                                        setSearchField(e.target.value);
                                    }}
                                    value={searchField}
                                    style={{
                                        backgroundImage: `url(${process.env.REACT_APP_DOMAIN}wp-content/plugins/asian-legacy-library-plugin/library-plugin-images/angle-down.svg)`
                                    }}
                                    className="relative top-0 z-10 w-full py-2 pr-3 overflow-hidden text-xs text-black bg-white bg-right bg-no-repeat border-black border-none appearance-none overflow-ellipsis md:text-lg h-11 focus:outline-none focus:ring-0 focus: border-b-solid "
                                >
                                    <option value="">All</option>
                                    <option value="data">Body Of Text</option>
                                    <option value="name">Author</option>
                                    <option value="title">Title</option>
                                    <option value="variant">
                                        Variant title
                                    </option>
                                    <option value="colophon">Colophon</option>
                                </select>
                            </div>
                            <input
                                className={`w-full py-2 sm:pl-40 pl-20 sm:pr-3   overflow-ellipsis overflow-hidden h-12 text-black border-b-2 focus:outline-none focus:ring-0 focus: border-solid border-black sm:text-lg text-sm ${
                                    isLoading ? 'cursor-wait ' : ''
                                } 
                                    ${
                                        activeOperators.val.indexOf('near') !==
                                        -1
                                            ? ' '
                                            : // ? 'text-opacity-40 cursor-not-allowed'
                                              ''
                                    }
                                `}
                                type="search"
                                placeholder="Search resources - titles, authors, topics..."
                                value={searchQuery}
                                onChange={(e) => {
                                    setSearchQuery(e.target.value);
                                }}
                                onKeyDown={(e) => {
                                    if (e.key === 'Enter') {
                                        if (activePage === 1) {
                                            fetchData();
                                        } else {
                                            setActivePage(1);
                                        }
                                    }
                                }}
                                readOnly={isLoading}
                                // disabled={
                                //     activeOperators.val.indexOf('near') !== -1
                                // }
                                onBlur={(e) =>
                                    setTimeout(() => {
                                        setShowRecentSearch(false);
                                    }, 200)
                                }
                                onFocus={(e) => setShowRecentSearch(true)}
                            />
                            <div className="hidden text-right md:block">
                                <button
                                    type="button"
                                    className="px-4 py-1 mt-1 text-sm font-semibold uppercase border border-solid border-green text-green hover:bg-green hover:text-white"
                                    onClick={() => {
                                        if (activePage === 1) {
                                            fetchData();
                                        } else {
                                            setActivePage(1);
                                        }
                                    }}
                                >
                                    Search
                                </button>
                            </div>

                            {/* Reset search button  */}
                            {data.length ||
                            searchQuery ||
                            filterTree.length ||
                            (activeOperators.val.length &&
                                values.val.length &&
                                hasQuery) ||
                            authorFilter.length ||
                            languageFilter.length ||
                            sourceFilter !== '' ||
                            collectionFilter !== '' ||
                            collectionRamachandraFilter !== '' ||
                            assetsFilter.length ? (
                                <p
                                    onClick={(e) => {
                                        e.preventDefault();
                                        setData([]);
                                        setIsLoading(true);
                                        setSearchQuery('');
                                        setCollectionFilter('');

                                        setCollectionRamachandraFilter('');
                                        setBranch1('');
                                        setBranch2('');
                                        setBranch3('');
                                        setBranch4('');
                                        setBranch5('');
                                        setFilterTree([]);
                                        setActiveOperators({ val: [] });
                                        setValues({ val: [] });
                                        setLanguageFilter([]);
                                        setSourceFilter('');
                                        setAssetsFilter([]);
                                        setAuthorFilter([]);
                                        setInit(false);
                                        setShowLanguages(false);
                                        setTreeLanguage('');
                                        setIsLoading(false);
                                        setHasQuery(false);
                                    }}
                                    className="absolute bg-white right-0 sm:right-2.5 sm:top-3 top-3.5 cursor-pointer"
                                >
                                    <img
                                        src={`${process.env.REACT_APP_DOMAIN}wp-content/plugins/asian-legacy-library-plugin/library-plugin-images/close.svg`}
                                        alt="search"
                                    />
                                </p>
                            ) : (
                                <p className="absolute right-0 sm:top-1.5 top-2 bg-white cursor-pointer">
                                    <img
                                        src={`${process.env.REACT_APP_DOMAIN}wp-content/plugins/asian-legacy-library-plugin/library-plugin-images/search.svg`}
                                        alt="close"
                                    />
                                </p>
                            )}
                        </div>

                        {/* Recent Search  */}
                        <Transition
                            show={showRecentSearch && !searchQuery}
                            enter="transition ease-out duration-100"
                            enterFrom="transform opacity-0 scale-95"
                            enterTo="transform opacity-100 scale-100"
                            leave="transition ease-in duration-75"
                            leaveFrom="transform opacity-100 scale-100"
                            leaveTo="transform opacity-0 scale-95"
                            className="absolute z-10 w-full bg-white top-12"
                        >
                            <RecentSearch />
                        </Transition>

                        <div className="grid grid-cols-12 gap-2 pt-3 ">
                            <div className="relative col-span-12">
                                <div className="flex justify-between pt-0 text-xs">
                                    <div
                                        className={`flex flex-wrap ${
                                            isLoading
                                                ? 'pointer-events-none opacity-70'
                                                : ''
                                        }`}
                                    >
                                        {/* Subject tree pills  */}
                                        {branch1 && filterTree[0] && (
                                            <p className="relative inline-flex items-center px-4 py-2 m-0 mb-2 mr-2 text-sm text-black bg-gray-100 group ">
                                                {branch1.substring(0, 12)}
                                                {branch1.length > 12
                                                    ? '...'
                                                    : ''}
                                                <span className="absolute z-10 w-full max-w-full px-2 py-1 text-xs font-light text-white transform -translate-x-1/2 rounded opacity-0 pointer-events-none top-6 left-1/2 bg-green group-hover:opacity-100">
                                                    {branch1}
                                                </span>
                                                <img
                                                    src={`${process.env.REACT_APP_DOMAIN}wp-content/plugins/asian-legacy-library-plugin/library-plugin-images/close.svg`}
                                                    alt=""
                                                    onClick={(e) => {
                                                        e.preventDefault();
                                                        setRemoveClick(true);
                                                        filterTreefn(
                                                            branch1,
                                                            1,
                                                            true
                                                        );
                                                    }}
                                                    className="w-2.5 ml-2 cursor-pointer"
                                                />
                                            </p>
                                        )}
                                        {branch2 && filterTree[1] && (
                                            <p className="relative inline-flex items-center px-4 py-2 m-0 mb-2 mr-2 text-sm text-black bg-gray-100 group ">
                                                {branch2.substring(0, 12)}
                                                {branch2.length > 12
                                                    ? '...'
                                                    : ''}
                                                <span className="absolute z-10 w-full max-w-full px-2 py-1 text-xs font-light text-white transform -translate-x-1/2 rounded opacity-0 pointer-events-none top-6 left-1/2 bg-green group-hover:opacity-100">
                                                    {branch2}
                                                </span>
                                                <img
                                                    src={`${process.env.REACT_APP_DOMAIN}wp-content/plugins/asian-legacy-library-plugin/library-plugin-images/close.svg`}
                                                    alt=""
                                                    onClick={(e) => {
                                                        e.preventDefault();
                                                        setRemoveClick(true);
                                                        filterTreefn(
                                                            branch2,
                                                            2,
                                                            true
                                                        );
                                                    }}
                                                    className="w-2.5 ml-2 cursor-pointer"
                                                />
                                            </p>
                                        )}
                                        {branch3 && filterTree[2] && (
                                            <p className="relative inline-flex items-center px-4 py-2 m-0 mb-2 mr-2 text-sm text-black bg-gray-100 group ">
                                                {branch3.substring(0, 12)}
                                                {branch3.length > 12
                                                    ? '...'
                                                    : ''}
                                                <span className="absolute z-10 w-full max-w-full px-2 py-1 text-xs font-light text-white transform -translate-x-1/2 rounded opacity-0 pointer-events-none top-6 left-1/2 bg-green group-hover:opacity-100">
                                                    {branch3}
                                                </span>
                                                <img
                                                    src={`${process.env.REACT_APP_DOMAIN}wp-content/plugins/asian-legacy-library-plugin/library-plugin-images/close.svg`}
                                                    alt=""
                                                    onClick={(e) => {
                                                        e.preventDefault();
                                                        setRemoveClick(true);
                                                        filterTreefn(
                                                            branch3,
                                                            3,
                                                            true
                                                        );
                                                    }}
                                                    className="w-2.5 ml-2 cursor-pointer"
                                                />
                                            </p>
                                        )}
                                        {branch4 && filterTree[3] && (
                                            <p className="relative inline-flex items-center px-4 py-2 m-0 mb-2 mr-2 text-sm text-black bg-gray-100 group ">
                                                {branch4.substring(0, 12)}
                                                {branch4.length > 12
                                                    ? '...'
                                                    : ''}
                                                <span className="absolute z-10 w-full max-w-full px-2 py-1 text-xs font-light text-white transform -translate-x-1/2 rounded opacity-0 pointer-events-none top-6 left-1/2 bg-green group-hover:opacity-100">
                                                    {branch4}
                                                </span>
                                                <img
                                                    src={`${process.env.REACT_APP_DOMAIN}wp-content/plugins/asian-legacy-library-plugin/library-plugin-images/close.svg`}
                                                    alt=""
                                                    onClick={(e) => {
                                                        e.preventDefault();
                                                        setRemoveClick(true);
                                                        filterTreefn(
                                                            branch4,
                                                            4,
                                                            true
                                                        );
                                                    }}
                                                    className="w-2.5 ml-2 cursor-pointer"
                                                />
                                            </p>
                                        )}
                                        {branch5 && filterTree[4] && (
                                            <p className="relative inline-flex items-center px-4 py-2 m-0 mb-2 mr-2 text-sm text-black bg-gray-100 group ">
                                                {branch5.substring(0, 12)}
                                                {branch5.length > 12
                                                    ? '...'
                                                    : ''}
                                                <span className="absolute z-10 w-full max-w-full px-2 py-1 text-xs font-light text-white transform -translate-x-1/2 rounded opacity-0 pointer-events-none top-6 left-1/2 bg-green group-hover:opacity-100">
                                                    {branch5}
                                                </span>
                                                <img
                                                    src={`${process.env.REACT_APP_DOMAIN}wp-content/plugins/asian-legacy-library-plugin/library-plugin-images/close.svg`}
                                                    alt=""
                                                    onClick={(e) => {
                                                        e.preventDefault();
                                                        setRemoveClick(true);
                                                        filterTreefn(
                                                            branch5,
                                                            5,
                                                            true
                                                        );
                                                    }}
                                                    className="w-2.5 ml-2 cursor-pointer"
                                                />
                                            </p>
                                        )}

                                        {/* Query builder pill  */}
                                        {activeOperators?.val?.length !== 0 &&
                                            hasQuery && (
                                                <p className="inline-flex items-center px-4 py-2 m-0 mb-2 mr-2 text-sm text-black bg-gray-100">
                                                    Custom Query
                                                    <img
                                                        src={`${process.env.REACT_APP_DOMAIN}wp-content/plugins/asian-legacy-library-plugin/library-plugin-images/close.svg`}
                                                        alt=""
                                                        onClick={(e) => {
                                                            e.preventDefault();
                                                            setActivePage(1);
                                                            setValues({
                                                                val: []
                                                            });
                                                            setActiveOperators({
                                                                val: []
                                                            });
                                                            setHasQuery(false);

                                                            setRemoveClick(
                                                                true
                                                            );
                                                        }}
                                                        className="w-2.5 ml-2 cursor-pointer"
                                                    />
                                                </p>
                                            )}
                                    </div>
                                </div>

                                <div
                                    className={`absolute w-full md:-top-11 top-0 p-5 pt-10 bg-white    border  border-solid border-yellow z-10 ${
                                        showAdvanceSearch !== -1
                                            ? 'block'
                                            : 'hidden'
                                    }`}
                                >
                                    <div
                                        className="absolute right-4 top-3.5 cursor-pointer opacity-50"
                                        onClick={(e) => {
                                            e.preventDefault();
                                            setShowAdvanceSearch(-1);
                                            if (!hasQuery) {
                                                setActiveOperators({ val: [] });
                                                setValues({ val: [] });
                                            }
                                        }}
                                    >
                                        <img
                                            src={`${process.env.REACT_APP_DOMAIN}wp-content/plugins/asian-legacy-library-plugin/library-plugin-images/close.svg`}
                                            alt="close"
                                            className="w-3"
                                        />
                                    </div>
                                    <div>
                                        <div
                                            className={`${
                                                showAdvanceSearch === 1
                                                    ? 'block'
                                                    : 'hidden'
                                            }`}
                                        >
                                            <SubjectTree
                                                branch1={branch1}
                                                branch2={branch2}
                                                branch3={branch3}
                                                branch4={branch4}
                                                branch5={branch5}
                                                filterTreefn={filterTreefn}
                                            />
                                        </div>
                                        <div
                                            onKeyDown={(e) => {
                                                if (e.key === 'Enter') {
                                                    if (
                                                        values.val.indexOf(
                                                            ''
                                                        ) === -1 ||
                                                        (nearBy > 0 &&
                                                            nearBy <= 10)
                                                    ) {
                                                        updateFilters();
                                                    }
                                                }
                                            }}
                                            className={`${
                                                showAdvanceSearch === 2
                                                    ? 'block'
                                                    : 'hidden'
                                            }`}
                                        >
                                            <QueryBuilder />
                                        </div>
                                        {/* Action button  */}
                                        <div className="mt-10 space-x-4 text-right">
                                            <button
                                                onClick={(e) => {
                                                    e.preventDefault();
                                                    setShowAdvanceSearch(-1);
                                                    if (!hasQuery) {
                                                        setActiveOperators({
                                                            val: []
                                                        });
                                                        setValues({
                                                            val: []
                                                        });
                                                    }
                                                }}
                                                className="py-1 text-sm bg-white border-2 border-solid px-7 border-gray text-gray hover:bg-gray hover:text-black "
                                            >
                                                Cancel
                                            </button>

                                            <button
                                                onClick={(e) => {
                                                    updateFilters();
                                                }}
                                                className={`py-1 text-sm bg-white border-2 px-7 border-green text-green hover:bg-yellow hover: border-solid hover:border-yellow ${
                                                    values.val.indexOf('') ===
                                                        -1 ||
                                                    activeOperators.val.indexOf(
                                                        'near'
                                                    ) !== -1
                                                        ? ''
                                                        : 'pointer-events-none opacity-70'
                                                } `}
                                            >
                                                Apply
                                            </button>
                                        </div>
                                        {/* Notes  */}

                                        <div className="items-center justify-between sm:flex">
                                            <p className="relative flex items-center m-0 mt-3 text-xs text-gray group">
                                                <img
                                                    src={`${process.env.REACT_APP_DOMAIN}wp-content/plugins/asian-legacy-library-plugin/library-plugin-images/info.svg`}
                                                    alt="close"
                                                    className="w-5 mr-2 opacity-40"
                                                />
                                                <span className="absolute w-56 opacity-0 left-6 group-hover:opacity-100">
                                                    We understand you want to
                                                    build complex queries. This
                                                    feature is coming soon!
                                                </span>
                                            </p>
                                            <p className="mt-3 text-xs text-gray">
                                                All fields are required
                                            </p>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
};
export default Search;
