import { useEffect } from 'react';
import { useAPI } from '../../context/apiContext';
const Collections = () => {
    const {
        sourceLibraries,
        setSourceFilter,
        sourceFilter,
        collections,
        setCollectionFilter,
        collectionFilter,
        fetchData
    } = useAPI();

    useEffect(() => {
        if (sourceFilter || collectionFilter) {
            fetchData();
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [sourceFilter, collectionFilter]);
    return (
        <div className="text-center ">
            <h5 className="inline-block py-1 mb-6 text-base leading-10 text-black bg-gray-100 lg:px-32 px-14 text-opacity-90">
                Tibetan Input Text Collections
            </h5>
            <div className="items-center justify-center sm:flex ">
                {collections &&
                    collections.map((item, index) => (
                        <a
                            key={index}
                            className="inline-block mx-5 mb-3 text-base text-green"
                            href="#!"
                            onClick={(e) => {
                                e.preventDefault();
                                setCollectionFilter(item?.key);
                            }}
                        >
                            {item?.key}
                        </a>
                    ))}
            </div>
            <h5 className="inline-block py-1 mt-20 mb-6 text-base leading-10 text-black bg-gray-100 lg:px-20 px-14 text-opacity-90">
                Browse Source Libraries
            </h5>
            <div className="items-center justify-center md:flex">
                {sourceLibraries &&
                    sourceLibraries.map((item, index) => (
                        <a
                            key={index}
                            className="inline-block mx-5 mb-3 text-base text-green"
                            href="#!"
                            onClick={(e) => {
                                e.preventDefault();
                                setSourceFilter(item?.key);
                            }}
                        >
                            {item?.key}
                        </a>
                    ))}
            </div>
        </div>
    );
};
export default Collections;
