import { useEffect, useState } from 'react';

const Download = ({ setShowDownload, single, showDownload }) => {
    const [hasTxt, setHasTxt] = useState(false);
    useEffect(() => {
        async function getDownload() {
            let url = `https://api.asianlegacylibrary.org/resources/download/${single}?format=txt&force_download=true`;

            await fetch(url, {
                method: 'GET',
                headers: {
                    'Content-Type': 'application/json'
                }
            })
                .then((data) => {
                    if (data.status === 200) {
                        setHasTxt(true);
                    }
                })
                .catch((error) => {
                    setHasTxt(false);
                });

            // console.log(json);
        }
        if (showDownload) {
            getDownload();
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [showDownload]);
    return (
        <>
            <div className="fixed top-0 left-0 flex items-center justify-center w-screen h-screen p-10 z-999999 bg-green bg-opacity-60">
                <div className="relative w-full h-auto max-w-md max-h-full p-10 overflow-auto bg-white">
                    <p className="text-xl mb-7">SELECT DOWNLOAD FORMAT</p>
                    <div
                        className="absolute cursor-pointer right-6 top-6"
                        onClick={(e) => setShowDownload(false)}
                    >
                        <img
                            src={`${process.env.REACT_APP_DOMAIN}wp-content/plugins/asian-legacy-library-plugin/library-plugin-images/close.svg`}
                            alt=""
                            className="w-4"
                        />
                    </div>
                    <div className="divide-y divide-gray-200 ">
                        <p className="m-0 ">
                            <a
                                target="_blank"
                                className="underline"
                                href={`https://api.asianlegacylibrary.org/resources/download/${single}?format=json&force_download=true`}
                                rel="noreferrer"
                            >
                                Download JSON
                            </a>
                        </p>
                        {hasTxt && (
                            <p className="py-3 m-0">
                                <a
                                    target="_blank"
                                    className="underline"
                                    href={`https://api.asianlegacylibrary.org/resources/download/${single}?format=txt&force_download=true`}
                                    rel="noreferrer"
                                >
                                    Download TXT
                                </a>
                            </p>
                        )}
                    </div>
                </div>
            </div>
        </>
    );
};
export default Download;
