import { useState, useEffect } from 'react';

const IIIF = ({ src, size }) => {
    const [status, setStatus] = useState(0);
    useEffect(() => {
        var myRequest = new Request(src);

        fetch(myRequest).then(function (response) {
            setStatus(response.status);
        });
    }, [src]);
    return (
        <>
            {status === 200 ? (
                <iframe
                    title="Asian Legacy Library IIIF Viewer"
                    width="100%"
                    height={`${size === 'large' ? '750px' : '500px'}`}
                    src={
                        `${process.env.REACT_APP_DOMAIN}wp-content/plugins/asian-legacy-library-plugin/uv-assets/dist/all-uv.html?manifest=` +
                        src
                    }
                />
            ) : (
                <div className="p-10 text-center text-white bg-black bg-opacity-90">
                    <p className="text-xl">
                        Scans still being processed. <br />
                        Please email{' '}
                        <a
                            href="mailto:librarian@asianlegacylibrary.org"
                            className="text-white underline"
                        >
                            librarian@asianlegacylibrary.org
                        </a>{' '}
                        for status
                    </p>
                </div>
            )}
        </>
    );
};

export default IIIF;
