import React, { useState } from 'react';
import { Transition } from '@headlessui/react';
import { useAPI } from '../../context/apiContext';
const QueryBuilder = () => {
    const {
        activeOperators,
        setActiveOperators,
        values,
        setValues,
        // nearThis,
        nearThat,
        nearBy,
        setNearBy,
        setNearThat
        // setNearThis
    } = useAPI();

    const [showOperatorsMenu, setShowOperatorsMenu] = useState({
        open: false,
        index: 0
    });
    // Handle query value
    function handleChange(event) {
        let vals = [...values.val];
        vals[this] = event.target.value;
        setValues({ val: vals });
    }
    // Handle query operator
    function handleActiveOperators(i, p) {
        let vals = [...activeOperators.val];
        vals[i] = p;
        setActiveOperators({ val: vals });
    }

    const removeClick = (e, i) => {
        let vals = [...values.val];
        let operators = [...activeOperators.val];
        vals.splice(i, 1);
        operators.splice(i, 1);
        setValues({ val: vals });
        setActiveOperators({ val: operators });
    };
    // Create dynamic inputs
    function createInputs() {
        return values.val.map((el, i) => (
            <div key={i}>
                <div className="grid grid-cols-12 gap-2">
                    <div className="relative col-span-3">
                        <button
                            onClick={(e) =>
                                setShowOperatorsMenu({
                                    open: !showOperatorsMenu.open,
                                    index: i
                                })
                            }
                            className="m-0 py-2.5 md:px-4 px-2 bg-gray-100 text-black items-center mr-8 mb-2 w-full flex justify-between capitalize md:text-base text-sm"
                        >
                            {activeOperators.val[i]}
                            <img
                                src={`${process.env.REACT_APP_DOMAIN}wp-content/plugins/asian-legacy-library-plugin/library-plugin-images/angle.svg`}
                                className="inline-block w-3 transform rotate-90 "
                                alt=""
                            />
                        </button>
                        <Transition
                            show={
                                showOperatorsMenu?.open &&
                                showOperatorsMenu.index === i
                            }
                            enter="transition ease-out duration-100"
                            enterFrom="transform opacity-0 scale-95"
                            enterTo="transform opacity-100 scale-100"
                            leave="transition ease-in duration-75"
                            leaveFrom="transform opacity-100 scale-100"
                            leaveTo="transform opacity-0 scale-95"
                            className={`absolute w-full py-1 space-y-2 bg-gray-100 top-10 z-10`}
                        >
                            <button
                                onClick={() => {
                                    setShowOperatorsMenu({
                                        index: 0,
                                        open: false
                                    });
                                    handleActiveOperators(i, 'and');
                                    setNearBy(4);
                                    setNearThat('');
                                    setValues({ val: [''] });
                                }}
                                className="flex items-center w-full px-4 py-2 m-0 mr-8 text-sm text-black bg-gray-100 hover:bg-gray-200 md:text-base"
                            >
                                And
                            </button>
                            <button
                                onClick={() => {
                                    setShowOperatorsMenu({
                                        index: 0,
                                        open: false
                                    });
                                    handleActiveOperators(i, 'or');
                                    setValues({ val: [''] });
                                    setNearBy(4);
                                    setNearThat('');
                                }}
                                className="flex items-center w-full px-4 py-2 m-0 mr-8 text-sm text-black bg-gray-100 hover:bg-gray-200 md:text-base"
                            >
                                Or
                            </button>
                            <button
                                onClick={() => {
                                    setShowOperatorsMenu({
                                        index: 0,
                                        open: false
                                    });
                                    handleActiveOperators(i, 'not');
                                    setValues({ val: [''] });
                                    setNearBy(4);
                                    setNearThat('');
                                }}
                                className="flex items-center w-full px-4 py-2 m-0 mr-8 text-sm text-black bg-gray-100 hover:bg-gray-200 md:text-base"
                            >
                                Not
                            </button>
                            <button
                                onClick={() => {
                                    setShowOperatorsMenu({
                                        index: 0,
                                        open: false
                                    });
                                    setValues({ val: [''] });
                                    handleActiveOperators(i, 'near');
                                }}
                                className="flex items-center w-full px-4 py-2 m-0 mr-8 text-sm text-black bg-gray-100 hover:bg-gray-200 md:text-base"
                            >
                                Near
                            </button>
                        </Transition>
                    </div>

                    <div className={`relative col-span-8`}>
                        <div className="relative">
                            {activeOperators.val[i] === 'near' ? (
                                <>
                                    <div className="grid grid-cols-2 gap-2">
                                        {/* <input
                                            onChange={(e) =>
                                                setNearThis(e.target.value)
                                            }
                                            className="m-0 py-2.5 md:text-base text-sm px-4 bg-gray-100 text-black items-center mr-8 mb-2 w-full flex justify-between focus:outline-none focus:ring-0"
                                            type="text"
                                            placeholder="This"
                                            value={nearThis}
                                        /> */}
                                        <input
                                            onChange={(e) =>
                                                setNearThat(e.target.value)
                                            }
                                            className="m-0 py-2.5 px-4 md:text-base text-sm bg-gray-100 text-black items-center mr-8 mb-2 w-full flex justify-between focus:outline-none focus:ring-0"
                                            type="text"
                                            placeholder="Near to"
                                            value={nearThat}
                                        />
                                        <div>
                                            <input
                                                onChange={(e) => {
                                                    setNearBy(e.target.value);
                                                }}
                                                className="m-0 py-2.5 px-4 md:text-base text-sm bg-gray-100 text-black items-center mr-8 mb-2 w-full flex justify-between focus:outline-none focus:ring-0"
                                                type="number"
                                                placeholder="lines away from each other"
                                                max={10}
                                                min={0}
                                                value={nearBy}
                                                onBlur={(e) =>
                                                    setTimeout(() => {
                                                        if (nearBy >= 10) {
                                                            setNearBy(10);
                                                        } else if (nearBy < 0) {
                                                            setNearBy(0);
                                                        }
                                                    }, 700)
                                                }
                                            />
                                            {nearBy > 10 || nearBy < 0 ? (
                                                <p class="mt-0 text-xs text-red-600 absolute">
                                                    Value must be between 0 and
                                                    10
                                                </p>
                                            ) : (
                                                ''
                                            )}
                                        </div>
                                    </div>
                                </>
                            ) : (
                                <input
                                    className={`m-0 py-2.5 px-4 bg-gray-100 text-black md:text-base text-sm items-center mr-8 mb-2 w-full flex justify-between focus:outline-none focus:ring-0 ${
                                        activeOperators.val.indexOf('near') !==
                                        -1
                                            ? 'opacity-50 pointer-events-none'
                                            : ''
                                    }`}
                                    type="text"
                                    value={el || ''}
                                    onChange={handleChange.bind(i)}
                                />
                            )}
                        </div>
                    </div>

                    <span
                        onClick={(e) => {
                            e.preventDefault();
                            removeClick(e, i);
                        }}
                        className="inline-flex items-center justify-center col-span-1 py-2 m-0 mb-2 text-xs font-semibold text-black cursor-pointer sm:bg-gray-100 sm:px-3"
                    >
                        <img
                            src={`${process.env.REACT_APP_DOMAIN}wp-content/plugins/asian-legacy-library-plugin/library-plugin-images/close.svg`}
                            className="w-3"
                            alt=""
                        />
                    </span>
                </div>
                {/* {activeOperators.val[i] === 'near' ? (
                    <p className="mb-3 text-xs text-gray">
                        Near search can only be performed in isolation from
                        other filtering.
                    </p>
                ) : (
                    ''
                )} */}
            </div>
        ));
    }

    // Add new input
    const addClick = () => {
        setValues({ val: [...values.val, ''] });
        setActiveOperators({ val: [...activeOperators.val, 'and'] });
    };

    return (
        <>
            {createInputs()}

            <div className="text-right">
                {/* {values.val.indexOf('') === -1 && ( */}
                {values.val.length === 0 && (
                    <a
                        href="#!"
                        onClick={(e) => {
                            e.preventDefault();
                            addClick();
                        }}
                        className="inline-flex items-center mt-5 text-sm text-black "
                    >
                        <img
                            src={`${process.env.REACT_APP_DOMAIN}wp-content/plugins/asian-legacy-library-plugin/library-plugin-images/close.svg`}
                            className="inline-block w-2 mr-2 transform rotate-45"
                            alt="add"
                        />
                        Add Block
                    </a>
                )}
            </div>
        </>
    );
};
export default QueryBuilder;
