import React, { useEffect, useState, useRef } from 'react';

import { useAPI, useDebounce } from '../../context/apiContext';

import Navigation from './navigation';
import RelatedResources from './RelatedResources';
import AboutSourceLib from './AboutSourceLib';
import MetaData from './MetaData';
import Download from './Download';
import IIIF from './iiif';
import { Link } from 'react-router-dom';
let allFindings;
function SingleResult({ match }) {
    const myRef = useRef(null);
    const {
        singleData,
        setSingle,
        isLoading,
        single,
        globalSearch,
        setGlobalSearch,
        fetchURL,
        getSingleView
        // searchQuery
        // values
    } = useAPI();

    const { id } = match.params;

    const [copyState, setCopyState] = useState('Copy');
    const [position, setPosition] = useState(-1);

    // Set resource ID
    useEffect(() => {
        if (id) {
            setGlobalSearch('');
            setSingle(id);
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [id]);

    //Main element of data source
    const elem = document.querySelector('.h-50vh');
    //Reset search value
    useEffect(() => {
        setGlobalSearch('');
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);
    //Pharse data

    function cleanChar(val) {
        if (!val) return;
        let data = val
            .replaceAll(/^(@\w+)\s/g, '<br/><strong>$1</strong> ')
            .replaceAll(/\s(@\w+)\s/g, '<br/><strong>$1</strong><br/>');
        // .replaceAll(/\[\/n\]/gi, '<br/><br/>');

        data = data.replaceAll(
            `<em>${globalSearch.toUpperCase()}</em>`,
            `<em class="bg-gray-300">${globalSearch.toUpperCase()}</em>`
        );
        data = data.replaceAll(
            `<em>${globalSearch.toLowerCase()}</em>`,
            `<em class="bg-gray-300">${globalSearch.toLowerCase()}</em>`
        );
        data = data.replaceAll(
            `<em>${globalSearch}</em>`,
            `<em class="bg-gray-300">${globalSearch}</em>`
        );
        // if (values.val[0]) {
        //     data = data.replaceAll(
        //         `<em>${values.val[0].toUpperCase()}</em>`,
        //         `<em class="bg-yellow-light">${values.val[0].toUpperCase()}</em>`
        //     );
        //     data = data.replaceAll(
        //         `<em>${values.val[0].toLowerCase()}</em>`,
        //         `<em class="bg-yellow-light">${values.val[0].toLowerCase()}</em>`
        //     );
        //     data = data.replaceAll(
        //         `<em>${values.val[0]}</em>`,
        //         `<em class="bg-yellow-light">${values.val[0]}</em>`
        //     );
        // }
        // data = data.replaceAll(
        //     `<em>${
        //         searchQuery.charAt(0).toUpperCase() + searchQuery.slice(1)
        //     }</em>`,
        //     `<em class="bg-yellow-light">${
        //         searchQuery.charAt(0).toUpperCase() + searchQuery.slice(1)
        //     }</em>`
        // );

        return data;
    }

    let data = singleData[0];
    const [showModal, setShowModal] = useState(false);
    const [showDownload, setShowDownload] = useState(false);

    const debouncedQuery = useDebounce(globalSearch, 300);

    useEffect(() => {
        document.querySelector('html').scroll({
            top: 0,
            behavior: 'smooth'
        });
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);
    useEffect(() => {
        document.querySelector('html').scroll({
            top: 0,
            behavior: 'smooth'
        });
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [single]);

    // Update search state
    useEffect(() => {
        setPosition(-1);
        getSingleView();

        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [debouncedQuery]);

    //Copy URL funnction
    function copy(e) {
        e.preventDefault();
        var dummy = document.createElement('input'),
            text = window.location.href;

        document.body.appendChild(dummy);
        dummy.value = text;
        dummy.select();
        document.execCommand('copy');
        document.body.removeChild(dummy);
        setCopyState('Copied!');
        setTimeout(() => {
            setCopyState('Copy');
        }, 2000);
    }
    useEffect(() => {
        allFindings = document.querySelectorAll('.resource-search em');
        if (
            document.querySelectorAll('.resource-search') &&
            position !== -1 &&
            allFindings.length > 0
        ) {
            if (position < allFindings.length && position >= 0) {
                allFindings.forEach((item) => (item.style.fontWeight = '400'));
                allFindings[position].style.fontWeight = 'bold';
                let elemP =
                    allFindings[position].getBoundingClientRect().top -
                    elem.getBoundingClientRect().top;
                let yP = elem.scrollTop;

                let diff = elemP + yP;

                elem.scroll({
                    top: diff,
                    behavior: 'smooth'
                });
            }
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [position, debouncedQuery, single]);

    return (
        <>
            {/* Show loader  */}
            {isLoading && (
                <div className="fixed top-0 left-0 z-40 flex items-center justify-center w-full h-screen text-2xl text-center bg-white bg-opacity-90">
                    <div>
                        <img
                            src={`${process.env.REACT_APP_DOMAIN}wp-content/plugins/asian-legacy-library-plugin/library-plugin-images/loader.svg`}
                            className="block w-10 mx-auto"
                            alt=""
                        />{' '}
                        Loading...
                    </div>
                </div>
            )}
            <div className="px-4 py-5 bg-yellow-light ">
                <div className="mx-auto max-w-7xl">
                    <Link
                        to="/"
                        className="inline-block text-sm cursor-pointer"
                    >
                        Back to {fetchURL ? 'results' : 'search'}
                    </Link>
                    <div className="mt-5 mb-3 md:mt-12">
                        <div className="grid md:grid-cols-12">
                            <div className="md:col-span-7">
                                {data?._source?.['bibframe:maintitle'] && (
                                    <h3
                                        className="pl-0 m-0 text-xl md:text-3xl md:leading-9"
                                        dangerouslySetInnerHTML={{
                                            __html: cleanChar(
                                                data?._source?.[
                                                    'bibframe:maintitle'
                                                ] || '--'
                                            )
                                        }}
                                    />
                                )}
                            </div>
                            <div className="mt-5 md:pl-8 md:mt-0 md:col-span-5">
                                <p className="mb-1 text-sm text-gray-500 uppercase md:text-lg">
                                    AUTHOR
                                </p>
                                <p
                                    className="text-sm md:text-xl md:leading-8"
                                    dangerouslySetInnerHTML={{
                                        __html:
                                            data?._source?.[
                                                'bibframe:person'
                                            ] || '--'
                                    }}
                                />{' '}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="">
                {/* Navigation  */}
                <Navigation />
                <div className="px-4 mx-auto mt-0 max-w-7xl ">
                    {data?._source?.['all:items']?.['bibframe:datasource'] && (
                        <div className="flex items-center justify-start mb-10">
                            <div className="relative w-full max-w-md">
                                <input
                                    className="w-full px-0 py-2 text-base border-b-2 border-gray-400 border-solid focus:outline-none focus:ring-0 focus:"
                                    type="search"
                                    placeholder="Search.."
                                    onChange={(e) => {
                                        setGlobalSearch(e?.target?.value);
                                    }}
                                    value={globalSearch}
                                />
                                {/* <p className="text-xs text-gray">
                                    For an exact match, try enclosing your
                                    search strings in "double quotes".
                                </p> */}
                                <div className="absolute right-0 top-1.5 bg-white">
                                    <div className="flex items-center p-3 pr-0 space-x-3 bg-white ">
                                        <a
                                            href="#!"
                                            className="relative inline-block w-4 transform rotate-180 cursor-pointer group"
                                            onClick={(e) => {
                                                e.preventDefault();
                                                if (position > 0) {
                                                    setPosition(position - 1);
                                                }
                                            }}
                                        >
                                            <div className="absolute p-1 px-2 text-xs text-white transition-all transform -rotate-180 -translate-x-1/2 rounded opacity-0 left-1/2 bg-green text-12 -bottom-8 group-hover:opacity-100">
                                                Previous
                                            </div>
                                            <img
                                                src={`${process.env.REACT_APP_DOMAIN}wp-content/plugins/asian-legacy-library-plugin/library-plugin-images/ic_expand_more.svg`}
                                                alt=""
                                                className="w-4"
                                            />
                                        </a>{' '}
                                        <a
                                            href="#!"
                                            onClick={(e) => {
                                                e.preventDefault();
                                                if (
                                                    position <
                                                    allFindings.length
                                                ) {
                                                    setPosition(position + 1);
                                                }
                                            }}
                                            className="relative inline-block w-4 cursor-pointer group"
                                        >
                                            <div className="absolute p-1 px-2 text-xs text-white transition-all transform -translate-x-1/2 rounded opacity-0 left-1/2 bg-green text-12 -top-8 group-hover:opacity-100">
                                                Next
                                            </div>
                                            <img
                                                src={`${process.env.REACT_APP_DOMAIN}wp-content/plugins/asian-legacy-library-plugin/library-plugin-images/ic_expand_more.svg`}
                                                alt=""
                                                className="w-4"
                                            />
                                        </a>
                                    </div>
                                </div>
                            </div>
                        </div>
                    )}
                    {/* show colophon when no data source available  */}
                    <div className="grid md:grid-cols-12">
                        {!data?._source?.['all:items']?.[
                            'bibframe:datasource'
                        ] &&
                            !data?._source?.['all:items']?.[
                                'bibframe:electroniclocator'
                            ] &&
                            data?._source?.[
                                'bibframe:varianttitle@colophon'
                            ] && (
                                <div className="mb-8 md:col-span-12">
                                    <p className="mb-1 text-base text-gray-500">
                                        varianttitle@colophon:{' '}
                                    </p>
                                    <div
                                        className="block text-base break-words whitespace-pre-line resource-search"
                                        dangerouslySetInnerHTML={{
                                            __html: cleanChar(
                                                data?._source?.[
                                                    'bibframe:varianttitle@colophon'
                                                ]
                                            )
                                        }}
                                    />
                                </div>
                            )}
                        <div
                            className={`${
                                data?._source?.['all:items']?.[
                                    'bibframe:datasource'
                                ] ||
                                data?._source?.['all:items']?.[
                                    'bibframe:electroniclocator'
                                ]
                                    ? 'md:col-span-7'
                                    : 'md:col-span-12'
                            }`}
                        >
                            {data?._source?.['all:items']?.[
                                'bibframe:datasource'
                            ] && (
                                <>
                                    <div
                                        className="mb-6 overflow-x-hidden overflow-y-auto md:h-page h-50vh md:mb-14 "
                                        ref={myRef}
                                    >
                                        <div
                                            className="block leading-7 break-words whitespace-pre-line md:leading-8 md:text-lg resource-search"
                                            dangerouslySetInnerHTML={{
                                                __html: cleanChar(
                                                    data?._source?.[
                                                        'all:items'
                                                    ]?.['bibframe:datasource']
                                                )
                                            }}
                                        />
                                    </div>
                                    <div className="flex items-center justify-center mb-6 md:space-x-14 space-x-7 md:mb-14">
                                        <img
                                            src={`${process.env.REACT_APP_DOMAIN}wp-content/plugins/asian-legacy-library-plugin/library-plugin-images/ic_page_end.svg`}
                                            onClick={() => {
                                                elem.scroll({
                                                    top:
                                                        elem.scrollTop -
                                                        elem.scrollHeight,
                                                    behavior: 'smooth'
                                                });
                                            }}
                                            alt=""
                                            className="cursor-pointer "
                                        />
                                        <img
                                            src={`${process.env.REACT_APP_DOMAIN}wp-content/plugins/asian-legacy-library-plugin/library-plugin-images/ic_expand_more.svg`}
                                            onClick={() => {
                                                elem.scroll({
                                                    top: elem.scrollTop + 150,
                                                    behavior: 'smooth'
                                                });
                                            }}
                                            alt=""
                                            className="cursor-pointer "
                                        />
                                        <img
                                            onClick={() => {
                                                elem.scroll({
                                                    top: elem.scrollTop - 150,
                                                    behavior: 'smooth'
                                                });
                                            }}
                                            src={`${process.env.REACT_APP_DOMAIN}wp-content/plugins/asian-legacy-library-plugin/library-plugin-images/ic_expand_more.svg`}
                                            alt=""
                                            className="inline-block transform rotate-180 cursor-pointer"
                                        />
                                        <img
                                            src={`${process.env.REACT_APP_DOMAIN}wp-content/plugins/asian-legacy-library-plugin/library-plugin-images/ic_page_end.svg`}
                                            alt=""
                                            onClick={() => {
                                                elem.scroll({
                                                    top: elem.scrollHeight,
                                                    behavior: 'smooth'
                                                });
                                            }}
                                            className="inline-block transform rotate-180 cursor-pointer"
                                        />
                                    </div>
                                </>
                            )}
                            {!data?._source?.['all:items']?.[
                                'bibframe:datasource'
                            ] &&
                            data?._source?.['all:items']?.[
                                'bibframe:electroniclocator'
                            ] ? (
                                <IIIF
                                    size="large"
                                    src={`${data?._source?.['all:items']?.['bibframe:electroniclocator']}`}
                                />
                            ) : (
                                ''
                            )}
                        </div>
                        <div
                            className={`${
                                data?._source?.['all:items']?.[
                                    'bibframe:datasource'
                                ] ||
                                data?._source?.['all:items']?.[
                                    'bibframe:electroniclocator'
                                ]
                                    ? 'md:col-span-5 pt-7 md:pl-14'
                                    : 'md:col-span-12 pt-7'
                            }`}
                        >
                            <p className="flex items-center uppercase md:text-xl ">
                                ABOUT THIS RESOURCE{' '}
                                <a
                                    href="#!"
                                    onClick={(e) => {
                                        e.preventDefault();
                                        setShowModal(true);
                                    }}
                                    className="inline-block ml-3 text-xs underline capitalize text-green"
                                >
                                    See Resource Details
                                </a>
                            </p>{' '}
                            {showModal && (
                                <MetaData
                                    setShowModal={setShowModal}
                                    data={data?._source}
                                />
                            )}
                            {showDownload && (
                                <Download
                                    single={single}
                                    showDownload={showDownload}
                                    setShowDownload={setShowDownload}
                                />
                            )}
                            <div className="flex items-center my-5 space-x-7">
                                <span
                                    className="inline-block cursor-pointer"
                                    onClick={(e) => setShowDownload(true)}
                                >
                                    <img
                                        src={`${process.env.REACT_APP_DOMAIN}wp-content/plugins/asian-legacy-library-plugin/library-plugin-images/download.svg`}
                                        alt=""
                                    />
                                </span>
                                <span
                                    className="relative inline-block cursor-pointer group"
                                    onClick={copy}
                                >
                                    <div className="absolute p-1 px-2 text-xs text-white transition-all transform -translate-x-1/2 rounded opacity-0 left-1/2 bg-green text-12 -top-6 group-hover:opacity-100">
                                        {copyState}
                                    </div>
                                    <img
                                        src={`${process.env.REACT_APP_DOMAIN}wp-content/plugins/asian-legacy-library-plugin/library-plugin-images/link.svg`}
                                        alt=""
                                    />
                                </span>
                            </div>
                            <div className="grid grid-cols-2 gap-8">
                                <div>
                                    <p className="text-sm text-gray-500 mb-2.5">
                                        Source Library
                                    </p>
                                    <p
                                        className="text-base text-black "
                                        dangerouslySetInnerHTML={{
                                            __html:
                                                data?._source?.[
                                                    'bibframe:source'
                                                ] || '--'
                                        }}
                                    />
                                </div>
                                <div>
                                    <p className="text-sm text-gray-500 mb-2.5">
                                        Collection
                                    </p>
                                    <p
                                        className="text-base text-black "
                                        dangerouslySetInnerHTML={{
                                            __html:
                                                data?._source?.[
                                                    'bibframe:collection@acip'
                                                ] || '--'
                                        }}
                                    ></p>
                                </div>
                                <div>
                                    <p className="text-sm text-gray-500 mb-2.5">
                                        Resource ID
                                    </p>
                                    <p
                                        className="text-base text-black"
                                        dangerouslySetInnerHTML={{
                                            __html: data?._id || '--'
                                        }}
                                    ></p>
                                </div>
                                <div>
                                    <p className="text-sm text-gray-500 mb-2.5">
                                        Asset Type
                                    </p>
                                    <p
                                        className="text-base text-black capitalize"
                                        dangerouslySetInnerHTML={{
                                            __html:
                                                data?._source?.['all:items']?.[
                                                    'all:assettype'
                                                ] || '--'
                                        }}
                                    ></p>
                                </div>{' '}
                                <div>
                                    <p className="text-sm text-gray-500 mb-2.5">
                                        Digital Rights
                                    </p>
                                    <p
                                        className="text-base text-black capitalize"
                                        dangerouslySetInnerHTML={{
                                            __html:
                                                data?._source?.['all:items']?.[
                                                    'all:digitalrights'
                                                ] || '--'
                                        }}
                                    ></p>
                                </div>
                            </div>
                            {data?._source?.['all:items']?.[
                                'bibframe:electroniclocator'
                            ] &&
                            data?._source?.['all:items']?.[
                                'bibframe:datasource'
                            ] ? (
                                <>
                                    <p className="mt-24 mb-5 text-lg uppercase">
                                        SCAN GALLERY VIEW
                                    </p>
                                    <div className="relative w-full bg-cover">
                                        <IIIF
                                            src={`${data?._source?.['all:items']?.['bibframe:electroniclocator']}`}
                                        />
                                    </div>
                                </>
                            ) : (
                                ''
                            )}
                        </div>
                    </div>
                </div>
            </div>
            {data?._source?.['all:relatedworks'] && (
                <RelatedResources
                    relatedworks={data?._source?.['all:relatedworks']}
                />
            )}
            {data?._source?.['bibframe:source'] && (
                <AboutSourceLib lib={data?._source?.['bibframe:source']} />
            )}
        </>
    );
}
export default SingleResult;
